import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { stringContainsNumber } from '../../others/helper';

export default function BasicDetails({ dependencies: [setSpinner, setAlert] }) {
    const [details, setDetails] = useState({ name: "", email: "", phone: "" });
    const [errors, setErrors] = useState({});
    const defaultErr = "Please enter a value";
    const outlineRed = " outline-red";

    const submitHandler = e => {
        e.preventDefault();
        for (const item in details) {
            if (item !== "phone" && !details[item]) {
                errors[item] = defaultErr;
            }
        }
        for (const item in errors) {
            if (errors[item]) {
                setAlert({ class: "alert-danger", message: "Please fill required details" });
                return;
            }
        }
        axios.put('updateUserDetails', { ...details }).then(
            res => {
                setAlert({ class: "alert-success", message: res.data.message });
            },
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
            }
        );
    };

    const handleChange = e => {
        const { name, value } = e.target;
        setDetails({ ...details, [name]: value });

    };

    const validateFields = e => {
        const { name, value } = e.target;
        if (name !== "phone" && value === "") {
            setErrors({ ...errors, [name]: defaultErr });
            return;
        }

        if (name === "name") {
            let specialChars = /[!@#$%^&*()_+=[\]{};':"\\|<>/?]+/;
            if (stringContainsNumber(value)) {
                setErrors({ ...errors, [name]: "Name cannot contain numbers" });
                return;
            }
            else if (specialChars.test(value)) {
                setErrors({ ...errors, [name]: "Name cannot contain special characters" });
                return;
            }
        }

        setErrors({ ...errors, [name]: "" });
    };

    const getUserDetails = () => {
        setSpinner(true);
        axios.get('getUserDetails').then(
            res => {
                setSpinner(false);
                setDetails({ ...details, ...res.data });
            },
            err => console.log(err)
        );
    };

    useEffect(getUserDetails, []);

    return (
        <div className="container d-flex justify-content-center">
            <div className="card shadow p-3 col-12 col-md-8 col-lg-6 col-xl-5">
                <h5 className="pb-2">Basic Details</h5>
                <form className="form-horizontal" onSubmit={submitHandler}>
                    <div className="form-outline">
                        <input type="text" name="name" className={"form-control form-control-lg" + (errors.name ? outlineRed : "")}
                            onChange={handleChange} value={details.name} onBlur={validateFields} />
                        <label className="form-label floating-label">Name</label>
                    </div>
                    <div className="validation-error">{errors.name}</div>

                    <div className="form-outline">
                        <input type="email" name="name" className="form-control form-control-lg"
                            onChange={handleChange} value={details.email} onBlur={validateFields} disabled />
                        <label className="form-label floating-label">Email</label>
                    </div>
                    <div className="validation-error">{errors.email}</div>

                    <div className="form-outline">
                        <input type="number" name="phone" className={"form-control form-control-lg hidden-arrows" + (errors.phone ? outlineRed : "")}
                            onChange={handleChange} value={details.phone} onBlur={validateFields} />
                        <label className="form-label floating-label">Phone</label>
                    </div>
                    <div className="validation-error">{errors.phone}</div>
                    <div className="d-flex justify-content-end">
                        <button className="btn btn-primary" type="submit">Save</button>
                    </div>
                </form>
            </div>
        </div>
    );
}
