import React, { useState, useEffect} from 'react';
import { Navigate } from 'react-router-dom';

import { setDependencies, submitHandler, collectionModalBodyTemplate, getCollectionTypes } from './CollectionModals';

import '../../../../pages/UserForms/Forms.css';

export default function EditCollectionModal({ initialDetails, setModalContent, setAlert, getSocietyCollections }) {
    const [details, setDetails] = useState(initialDetails);
    const [errors, setErrors] = useState({});

    const [collectionTypes, setCollectionTypes] = useState([]);

    setDependencies([setModalContent, setAlert, getSocietyCollections, setDetails, setErrors, setCollectionTypes,
    details, errors, collectionTypes]);

    useEffect(()=>{
        getCollectionTypes();
    }, []);

    return (alert && alert.class === "alert-success") ? (<Navigate to={'/'} />) : (<div className="modal-content">
        <div className="modal-header">
            <h5 className="modal-title">Edit Collection</h5>
            <button type="button" className="btn-close" onClick={() => setModalContent(null)}>
            </button>
        </div>
        <form onSubmit={e=> submitHandler(e, 'updateCollection', 'PUT')}>
            {collectionModalBodyTemplate()}
            <div className="form-outline modal-footer d-flex justify-content-between">
                <button type="submit" className="btn btn-primary">Save</button>
                <button type="button" className="btn btn-primary" onClick={() => setModalContent(null)}>Cancel</button>
            </div>
        </form>
    </div>);
}