import { stringContainsNumber } from '../../../../others/helper';

import axios from 'axios';

let setModalContent, setAlert, getSocieties,
    setDetails, setErrors, setCountries, details, errors, countries;

const setDependencies = (dependencyArray) => {
    [setModalContent, setAlert, getSocieties,
        setDetails, setErrors, setCountries, details, errors, countries] = dependencyArray;
};

const submitHandler = (e, url, method) => {
    e.preventDefault();
    for (const item in details) {
        if (details[item] === "") {
            setAlert({ class: "alert-danger", message: "Please fill all the fields" });
            return;
        }
    }
    for (const item in errors) {
        if (errors[item] !== "") {
            setAlert({ class: "alert-danger", message: "Please fill correct details" });
            return;
        }
    }
    axios({
        url: url,
        method: method,
        data: details
    }).then(
        res => {
            setModalContent(null);
            getSocieties();
            setAlert({ class: "alert-success", message: res.data.message });
        },
        err => {
            if (err.response.data.message !== undefined) {
                setAlert({ class: "alert-danger", message: err.response.data.message });
            }
            else {
                setAlert({ class: "alert-danger", message: err.response.data });
            }
        }
    );
};

const getCountries = () => {
    axios.get('getCountries').then(
        res => {
            setCountries(res.data);
        },
        err => console.log(err)
    );
};

const handleChange = e => {
    const { name, value } = e.target;
    setDetails({ ...details, [name]: value });
};

const validateFields = e => {
    const { name, value } = e.target;
    if (value === "") {
        setErrors({ ...errors, [name]: "Please enter a value" });
        return;
    }

    if (name === "name") {
        let specialChars = /[!@#$%^&*()_+=[\]{};':"\\|<>/?]+/;
        if (stringContainsNumber(value)) {
            setErrors({ ...errors, [name]: "Name cannot contain numbers" });
            return;
        }
        else if (specialChars.test(value)) {
            setErrors({ ...errors, [name]: "Name cannot contain special characters" });
            return;
        }
    }
    setErrors({ ...errors, [name]: "" });
};

const societyModalBodyTemplate = () => {
    return <div className="modal-body">
        <div className="form-outline">
            <input type="text" className="form-control form-control-lg" name="name"
                onChange={handleChange} value={details.name} onBlur={validateFields} />
            <label className="form-label floating-label">Society Name</label>
        </div>
        <div className="validation-error">{errors.name}</div>
        <div className="form-outline">
            <input type="text" className="form-control form-control-lg" name="address"
                onChange={handleChange} value={details.address} onBlur={validateFields} />
            <label className="form-label floating-label">Address</label>
        </div>
        <div className="validation-error">{errors.address}</div>
        <div className="form-outline">
            <input type="number" className="form-control form-control-lg hidden-arrows" name="pin_code"
                onChange={handleChange} value={details.pin_code} onBlur={validateFields} />
            <label className="form-label floating-label">Pin Code</label>
        </div>
        <div className="validation-error">{errors.pin_code}</div>
        <div className="form-outline">
            <select className="form-select form-select-lg" name="countryId" data-value={details.countryId}
                onChange={handleChange} value={details.countryId} onBlur={validateFields}>
                <option style={{ display: "none" }}></option>
                {countries.map((item, i) => <option key={i} value={item.id}>{item.name}</option>)}
            </select>
            <label className="form-label floating-label">Country</label>
        </div>
        <div className="validation-error">{errors.country}</div>
    </div>;
};

export { setDependencies, societyModalBodyTemplate, submitHandler, getCountries };