import React, { useEffect, useState } from 'react';
import { setDependencies, setDetailsForExpenseTransaction} from './ExpenseModal';

import { getLongDate } from '../../../../others/helper';

export default function ViewExpenseModal({ expenseId, setModalContent }) {
  const [details, setDetails] = useState({
    id: expenseId,
    amount: "",
    senderId: "",
    receiverId: "",
    date: "",
    methodId: "",
    transactionComment: ""
  });
  const [errors, setErrors] = useState({});
  const [senderAccounts, setSenderAccounts] = useState([]);
  const [receiverAccounts, setReceiverAccounts] = useState([]);
  const [paymentMethods, setPaymentMethods] = useState([]);

  setDependencies([setModalContent, null, null, setDetails, setErrors,
    setSenderAccounts, setReceiverAccounts, setPaymentMethods, details,
    errors, senderAccounts, receiverAccounts, paymentMethods]);

  useEffect(() => {
    setDetailsForExpenseTransaction(expenseId);
  }, []);

  return (
    <div className="modal-content">
      <div className="modal-header">
        <h5 className="modal-title">View Expense</h5>
        <button type="button" className="btn-close" onClick={() => setModalContent(null)}>
        </button>
      </div>
      <form>
      <div className="modal-body">
    <div className="form-outline">
      <input type="number" className="form-control form-control-lg hidden-arrows" name="amount"
        value={details.amount} disabled />
      <label className="form-label floating-label">Amount</label>
    </div>
    <div className="validation-error">{errors.amount}</div>
    <div className="form-outline">
      <select className="form-select form-select-lg" name="receiverId" data-value={details.receiverId}
        value={details.receiverId} disabled >
        <option style={{ display: "none" }}></option>
        {receiverAccounts.map((item, i) => <option key={i} value={item.id}>{item.name}</option>)}
      </select>
      <label className="form-label floating-label">Expense Head</label>
    </div>
    <div className="validation-error">{errors.receiverId}</div>
    <div className="form-outline">
      <input type="text" className="form-control form-control-lg" name="senderEmail"
        value={details.senderEmail} list="sender-account-suggestions" disabled/>
      <datalist id="sender-account-suggestions">
        {senderAccounts.length > 0 && senderAccounts.map((senderAccount, i) =>
          <option key={i} value={senderAccount.email}>{senderAccount.name}</option>)}
      </datalist>
      <label className="form-label floating-label">Sender</label>
    </div>
    <div className="validation-error">{errors.senderEmail}</div>
    <div className="form-outline">
      <input type="date" className="form-control form-control-lg datepicker" name="date"
        value={details.date} max={(new Date()).toISOString().split("T")[0]} disabled />
      <label className="date-label">{getLongDate(details.date)}</label>
      <label className="form-label floating-label">Payment Date</label>
    </div>
    <div className="validation-error">{errors.date}</div>
    <div className="form-outline">
      <select className="form-select form-select-lg" name="methodId" data-value={details.methodId}
        value={details.methodId} disabled >
        <option style={{ display: "none" }}></option>
        {paymentMethods.map((item, i) => <option key={i} value={item.id}>{item.method}</option>)}
      </select>
      <label className="form-label floating-label">Payment Method</label>
    </div>
    <div className="validation-error">{errors.methodId}</div>
    <div className="form-outline">
      <textarea type="text" className="form-control form-control-lg" name="transactionComment" data-value={details.transactionComment}
        value={details.transactionComment} disabled/>
      <label className="form-label floating-label">Comment</label>
    </div>
  </div>
        <div className="form-outline modal-footer d-flex justify-content-end">
          <button type="button" className="btn btn-primary" onClick={() => setModalContent(null)}>Cancel</button>
        </div>
      </form>
    </div>
  );
}
