import React, { useState } from 'react';
import axios from 'axios';

export default function ChangePassword({ dependencies: [setSpinner, setAlert] }) {
    const [details, setDetails] = useState({ currPassword: "", newPassword: "" });
    const [confirmPassword, setConfirmPassword] = useState('');
    const [errors, setErrors] = useState({});
    const defaultErr = "Please enter a value";
    const outlineRed = " outline-red";

    const submitHandler = e => {
        e.preventDefault();
        for (const item in details) {
            if (!details[item]) {
                errors[item] = defaultErr;
            }
        }
        for (const item in errors) {
            if (errors[item]) {
                setAlert({ class: "alert-danger", message: "Please fill required details" });
                return;
            }
        }
        axios.put('changePassword', { ...details }).then(
            res => {
                setAlert({ class: "alert-success", message: res.data.message });
            },
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
            }
        );
    };

    const handleChange = e => {
        const { name, value } = e.target;
        setDetails({ ...details, [name]: value });
    };

    const validateFields = e => {
        const { name, value } = e.target;
        if (value === "") {
            setErrors({ ...errors, [name]: defaultErr });
            return;
        }

        else if (name === "confirm_password") {
            if (details.newPassword !== value) {
                setErrors({ ...errors, [name]: "Password do not match" });
                return;
            }
        }

        setErrors({ ...errors, [name]: "" });
    };

    return (
        <div className="container d-flex justify-content-center mt-3">
            <div className="card shadow p-3 col-12 col-md-8 col-lg-6 col-xl-5">
                <h5 className="pb-2">Change Password</h5>
                <form className="form-horizontal" onSubmit={submitHandler}>
                    <div className="form-outline">
                        <input type="password" name="currPassword" className={"form-control form-control-lg" + (errors.currPassword ? outlineRed : "")}
                            onChange={handleChange} value={details.currPassword} onBlur={validateFields} />
                        <label className="form-label floating-label" >Current Password</label>
                    </div>
                    <div className="validation-error">{errors.currPassword}</div>

                    <div className="form-outline">
                        <input type="password" name="newPassword" className={"form-control form-control-lg" + (errors.newPassword ? outlineRed : "")}
                            onChange={handleChange} value={details.newPassword} onBlur={validateFields} />
                        <label className="form-label floating-label" >New Password</label>
                    </div>
                    <div className="validation-error">{errors.newPassword}</div>

                    <div className="form-outline">
                        <input type="password" name="confirm_password" className={"form-control form-control-lg" + (errors.confirm_password ? outlineRed : "")}
                            onChange={e => setConfirmPassword(e.currentTarget.value)} value={confirmPassword} onBlur={validateFields} />
                        <label className="form-label floating-label" >Confirm New Password</label>
                    </div>
                    <div className="validation-error">{errors.confirm_password}</div>
                    <div className="d-flex justify-content-end">
                        <button className="btn btn-primary" type="submit">Change</button>
                    </div>
                </form>
            </div>
        </div>
    );
}
