import './App.css';
import './bootstrap-overidden.css';
import {
  Home, LoginForm, SignUpForm, FlatList, Collections, CollectionSummary,
  Incomes, Expenses, Transactions, SocietyUsers, UserDetails, ForgotPassword, About
} from './pages/pages';
import { AppHeader, Loading, Modal, Alert, NavBar } from './components/components';
import { Route, Routes } from "react-router-dom";
import React, { useState, useEffect } from 'react';
import axios from 'axios';

function App() {
  const [user, setUser] = useState({ "user_full_name": null });
  const [spinner, setSpinner] = useState(true);
  const [modalContent, setModalContent] = useState(null);
  const [alert, setAlert] = useState({ class: "", message: "" });

  useEffect(() => {
    getUser();
  }, []);

  const getUser = () => {
    if (localStorage.getItem('token')) {
      axios.defaults.headers.common['Authorization'] = `Bearer ${localStorage.getItem('token')}`;
      axios.get('getUserName').then(
        res => setUser(res.data),
        err => setUser({ "user_full_name": "" })
      );
    }
    else {
      setUser({ "user_full_name": "" });
    }
  };
  return (
    <div className="App">
      <Alert alert={alert} setAlert={setAlert}></Alert>
      <NavBar user={user} setUser={setUser} />
      <AppHeader />
      <Loading spinner={spinner} />
      <Modal modalContent={modalContent} />
      <div className="pt-3 mb-5">
        <Routes>
          <Route path="login" element={<LoginForm
            dependencies={[getUser, user, setSpinner, setAlert]} />} />
          <Route path="signup" element={<SignUpForm
            dependencies={[setSpinner, setAlert]} />} />
          <Route path="forgot-password" element={<ForgotPassword
            dependencies={[setSpinner, setAlert]} />} />
          {['home', '/'].map(path =>
            <Route key={path} path={path}
              element={<Home
                dependencies={[user, setModalContent, setSpinner, setAlert]} />
              } />)}
          <Route path="about" element={<About
            dependencies={[setSpinner]} />} />
          <Route path="societies/:societyId" element={<FlatList
            dependencies={[setModalContent, setSpinner, setAlert]} />} />
          <Route path="societies/:societyId/users" element={<SocietyUsers
            dependencies={[setModalContent, setSpinner, setAlert]} />} />
          <Route path="societies/:societyId/collections" element={<Collections
            dependencies={[setModalContent, setSpinner, setAlert]} />} />
          <Route path="collections/:collectionId" element={<CollectionSummary
            dependencies={[setSpinner, setAlert]} />} />
          <Route path="collections/:collectionId/incomes" element={<Incomes
            dependencies={[setModalContent, setSpinner, setAlert]} />} />
          <Route path="collections/:collectionId/expenses" element={<Expenses
            dependencies={[setModalContent, setSpinner, setAlert]} />} />
          <Route path="societies/:societyId/transactions" element={<Transactions
            dependencies={[setSpinner, setAlert]} />} />
          <Route path="user-details" element={<UserDetails
            dependencies={[setSpinner, setAlert]} />} />
        </Routes>
      </div>
    </div>
  );
}

