import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import { AddSocietyUserModal, EditSocietyUserModal, ViewSocietyUserModal, DeleteModal } from '../../components/components';

export default function SocietyUsers({ dependencies: [setModalContent, setSpinner, setAlert] }) {
  const [societyUsers, setSocietyUsers] = useState([]);
  const { societyId } = useParams();

  const getSocietyUsers = () => {
    setSpinner(true);
    axios.get(`getSocietyUsers/${societyId}`).then(
      res => {
        setSocietyUsers(res.data);
        setSpinner(false);
      },
      err => {
        if (err.response.data.message !== undefined) {
          setAlert({ class: "alert-danger", message: err.response.data.message });
        }
        else {
          setAlert({ class: "alert-danger", message: err.response.data });
        }
        setSpinner(false);
      }
    );
  };
  useEffect(getSocietyUsers, []);
  return (
    <>
      <h3 className="d-flex justify-content-center">
        <span className='text-watermark'>Society Users</span>
      </h3>
      <div className="card shadow mx-3 table-responsive">
        <table className="table table-striped mb-2">
          <thead>
            <tr>
              <th scope="col">Name</th>
              <th scope="col">Email ID</th>
              <th scope="col">Action</th>
            </tr>
          </thead>
          <tbody>
            {societyUsers.length > 0 && societyUsers.map((societyUser, i) => {
              return <tr key={societyUser.id}>
                <td><div role="button" className={`btn-sm shadow ${societyUser.isAdmin ? "btn-primary" : "btn-secondary"}`}
                  onClick={() => setModalContent(<ViewSocietyUserModal
                    initialDetails={societyUser}
                    setModalContent={setModalContent}
                  />)}
                >
                  {societyUser.name}</div></td>
                <td><div className="btn-sm btn-purple">{societyUser.email}</div></td>
                <td><i role="button" className="btn-sm fa fa-edit text-primary"
                  onClick={() => setModalContent(<EditSocietyUserModal
                    initialDetails={societyUser}
                    existingUsers={societyUsers}
                    setModalContent={setModalContent}
                    setAlert={setAlert}
                    getSocietyUsers={getSocietyUsers}
                  />)}
                ></i>
                  <i role="button" className="btn-sm fa fa-trash-alt text-danger"
                    onClick={() => setModalContent(<DeleteModal
                      deleteUrl={`/deleteSocietyUser/${societyUser.id}`}
                      entityName={`user ${societyUser.email}`}
                      setModalContent={setModalContent}
                      setAlert={setAlert}
                      getEntities={getSocietyUsers}
                    />)}
                  ></i></td>
              </tr>;
            })

            }
          </tbody>
        </table>
      </div>
      <div className="text-center">
        <button type="button" className="btn btn-primary encircle-lg"
          onClick={() => setModalContent(<AddSocietyUserModal
            societyId={societyId}
            existingUsers={societyUsers}
            setModalContent={setModalContent}
            setAlert={setAlert}
            getSocietyUsers={getSocietyUsers}
          />)}>
          <i className="fa fa-plus"></i>
        </button>
      </div>
    </>
  );
}
