import React, { useEffect, useState } from 'react';
import axios from 'axios';

export default function SecurityQuestions({ dependencies: [setAlert] }) {
    const [details, setDetails] = useState({ currentPassword: "", questions: [] });
    const [target, reRender] = useState();
    const [errors, setErrors] = useState({});
    const [securityQuestions, setSecurityQuestions] = useState([]);
    const defaultErr = "Please enter a value";
    const outlineRed = " outline-red";

    const submitHandler = e => {
        e.preventDefault();
        for (const item in details) {
            if (!details[item]) {
                errors[item] = defaultErr;
            }
        }
        for (const item in errors) {
            if (errors[item]) {
                setAlert({ class: "alert-danger", message: "Please fill required details" });
                return;
            }
        }

        const questions = document.getElementsByClassName("securityQuestion");
        const answers = document.getElementsByClassName("answer");

        details.questions = [];
        for(let i=0; i< questions.length; i++){
            details.questions.push({id: questions[i].value, answer: answers[i].value});
        }

        axios.post('setSecurityQuestions', { ...details }).then(
            res => {
                setAlert({ class: "alert-success", message: res.data.message });
            },
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
            }
        );
    };

    const handleChange = e => {
        const { name, value } = e.target;
        setDetails({ ...details, [name]: value });

    };

    const validateFields = e => {
        const { name, value } = e.target;
        if (value === "") {
            setErrors({ ...errors, [name]: defaultErr });
            return;
        }
        setErrors({ ...errors, [name]: "" });
    };

    const getSecurityQuestions = () => {
        axios.get('getSecurityQuestions').then(
            res => setSecurityQuestions(res.data),
            err => console.log(err)
        );
    };

    const setQuestionSuggestion = target => {
        const usedQuestions = [...document.getElementsByClassName("securityQuestion")]
        .map(q => target && q.value!==target.value?q.value: "");
        return securityQuestions.map((item, i) =>
        <option key={i} value={item.id}
        style={{display: usedQuestions.includes(item.id.toString())?"none":"block"}}>{item.question}</option>);
    };
    useEffect(getSecurityQuestions, []);

    return (
        <div className="container d-flex justify-content-center mt-3">
            <div className="card shadow p-3 col-12 col-md-8 col-lg-6 col-xl-5">
                <h5 className="pb-2">Set Security Questions</h5>
                <form className="form-horizontal" onSubmit={submitHandler}>
                    <div className="form-outline">
                        <input type="password" name="currentPassword" className={"form-control form-control-lg" + (errors.currentPassword ? outlineRed : "")}
                            onChange={handleChange} value={details.currentPassword} onBlur={validateFields} />
                        <label className="form-label floating-label" >Current Password</label>
                    </div>
                    <div className="validation-error">{errors.currentPassword}</div>

                    <div className="form-outline mb-3">
                        <select className="form-select form-select-lg securityQuestion" name="question1"
                         onFocus={e=>reRender(e.currentTarget)}>
                            <option style={{ display: "none" }}></option>
                            {setQuestionSuggestion(target)}
                        </select>
                        <label className="form-label floating-label">Question 1</label>
                    </div>

                    <div className="form-outline">
                        <input type="text" className="form-control form-control-lg answer" name="answer1"
                            onBlur={validateFields} />
                        <label className="form-label floating-label">Answer 1</label>
                    </div>
                    <div className="validation-error mb-3">{errors.answer1}</div>

                    <div className="form-outline mb-3">
                        <select className="form-select form-select-lg securityQuestion" name="question2"
                         onFocus={e=>reRender(e.currentTarget)}>
                            <option style={{ display: "none" }}></option>
                            {setQuestionSuggestion(target)}
                        </select>
                        <label className="form-label floating-label">Question 2</label>
                    </div>

                    <div className="form-outline">
                        <input type="text" className="form-control form-control-lg answer" name="answer2"
                            onBlur={validateFields} />
                        <label className="form-label floating-label">Answer 2</label>
                    </div>
                    <div className="validation-error mb-3">{errors.answer2}</div>

                    <div className="form-outline mb-3">
                        <select className="form-select form-select-lg securityQuestion" name="question3"
                         onFocus={e=>reRender(e.currentTarget)}>
                            <option style={{ display: "none" }}></option>
                            {setQuestionSuggestion(target)}
                        </select>
                        <label className="form-label floating-label">Question 3</label>
                    </div>

                    <div className="form-outline">
                        <input type="text" className="form-control form-control-lg answer" name="answer3"
                            onBlur={validateFields} />
                        <label className="form-label floating-label">Answer 3</label>
                    </div>
                    <div className="validation-error mb-3">{errors.answer3}</div>

                    <div className="form-outline mb-3">
                        <select className="form-select form-select-lg securityQuestion" name="question4"
                         onFocus={e=>reRender(e.currentTarget)}>
                            <option style={{ display: "none" }}></option>
                            {setQuestionSuggestion(target)}
                        </select>
                        <label className="form-label floating-label">Question 4</label>
                    </div>

                    <div className="form-outline">
                        <input type="text" className="form-control form-control-lg answer" name="answer4"
                            onBlur={validateFields} />
                        <label className="form-label floating-label">Answer 4</label>
                    </div>
                    <div className="validation-error mb-3">{errors.answer4}</div>

                    <div className="form-outline mb-3">
                        <select className="form-select form-select-lg securityQuestion" name="question5"
                         onFocus={e=>reRender(e.currentTarget)}>
                            <option style={{ display: "none" }}></option>
                            {setQuestionSuggestion(target)}
                        </select>
                        <label className="form-label floating-label">Question 5</label>
                    </div>

                    <div className="form-outline">
                        <input type="text" className="form-control form-control-lg answer" name="answer5"
                            onBlur={validateFields} />
                        <label className="form-label floating-label">Answer 5</label>
                    </div>
                    <div className="validation-error mb-3">{errors.answer5}</div>

                    <div className="d-flex justify-content-end">
                        <button className="btn btn-primary" type="submit">Save</button>
                    </div>
                </form>
            </div>
        </div>
    );
}