import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";

import { stringContainsNumber } from '../../others/helper';

import './Forms.css';

export default function SignUpForm({ dependencies: [setSpinner, setAlert] }) {
    const [details, setDetails] = useState({ name: "", email: "", phone: "", password: "" });
    const [confirm_password, setConfirmPassword] = useState('');
    const [errors, setErrors] = useState({});

    const defaultErr = "Please enter a value";
    const outlineRed = " outline-red";
    const siteKey = process.env.REACT_APP_SITE_KEY;

    useEffect(() => {
        setSpinner(false);
        const script = document.createElement('script');

        script.src = "https://www.google.com/recaptcha/api.js";
        script.async = true;

        document.body.appendChild(script);

        return () => {
            document.body.removeChild(script);
        };
    }, []);

    const submitHandler = e => {
        e.preventDefault();
        for (const item in details) {
            if (item !== "phone" && !details[item]) {
                errors[item] = defaultErr;
            }
        }
        if (!confirm_password) {
            errors.confirm_password = defaultErr;
        }
        for (const item in errors) {
            if (errors[item]) {
                setAlert({ class: "alert-danger", message: "Please fill required details" });
                return;
            }
        }
        const captchaField = document.getElementsByName("g-recaptcha-response")[0];
        if (captchaField && captchaField.value) {
            details['captchaToken'] = captchaField.value;
        }
        else {
            setAlert({ class: "alert-danger", message: "Please verify captcha" });
            return false;
        }
        axios.post('signupUser', { ...details }).then(
            res => {
                setAlert({ class: "alert-success", message: res.data.message });
            },
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
            }
        );
    };

    const handleChange = e => {
        const { name, value } = e.target;
        setDetails({ ...details, [name]: value });
    };

    const validateFields = e => {
        const { name, value } = e.target;
        if (name !== "phone" && value === "") {
            setErrors({ ...errors, [name]: defaultErr });
            return;
        }

        if (name === "name") {
            let specialChars = /[!@#$%^&*()_+=[\]{};':"\\|<>/?]+/;
            if (stringContainsNumber(value)) {
                setErrors({ ...errors, [name]: "Name cannot contain numbers" });
                return;
            }
            else if (specialChars.test(value)) {
                setErrors({ ...errors, [name]: "Name cannot contain special characters" });
                return;
            }
        }
        else if (name === "email") {
            let validEmail = /^\S+@\S+\.\S+$/;
            if (!validEmail.test(value)) {
                setErrors({ ...errors, [name]: "Please enter a valid email id" });
                return;
            }
        }
        else if (name === "phone") {
            if (value !== "" && value.length < 10) {
                setErrors({ ...errors, [name]: "Please enter a valid phone number" });
                return;
            }
        }
        else if (name === "password") {
            if (value.length < 8) {
                setErrors({ ...errors, [name]: "Please enter minimum 8 characters" });
                return;
            }
        }
        else if (name === "confirm_password") {
            if (details.password !== value) {
                setErrors({ ...errors, [name]: "Password do not match" });
                return;
            }
        }
        setErrors({ ...errors, [name]: "" });
    };

    return (
        <section className="vh-100 w-100" style={{ backgroundColor: '#508bfc', marginTop: "-1rem" }}>
            <div className="container py-5">
                <div className="row d-flex justify-content-center align-items-center h-100">
                    <div className="col-12 col-md-8 col-lg-6 col-xl-5">
                        <div className="card shadow-2-strong" style={{ borderRadius: '1rem' }}>
                            <div className="card-body p-5 text-center">
                                <h3 className="p-2">Sign up</h3>
                                <form id="signupform" className="form-horizontal"
                                    onSubmit={submitHandler}>

                                    <div className="form-outline">
                                        <input type="text" id="name" name="name" className={"form-control form-control-lg" + (errors.name ? outlineRed : "")}
                                            onChange={handleChange} value={details.name} onBlur={validateFields} />
                                        <label className="form-label floating-label" htmlFor="name">Name</label>
                                    </div>
                                    <div className="validation-error">{errors.name}</div>

                                    <div className="form-outline">
                                        <input type="email" id="email" name="email" className={"form-control form-control-lg" + (errors.email ? outlineRed : "")}
                                            onChange={handleChange} value={details.email} onBlur={validateFields} />
                                        <label className="form-label floating-label" htmlFor="email">Email</label>
                                    </div>
                                    <div className="validation-error">{errors.email}</div>

                                    <div className="form-outline">
                                        <input type="number" id="phone" name="phone" className={"form-control form-control-lg hidden-arrows" + (errors.phone ? outlineRed : "")}
                                            onChange={handleChange} value={details.phone} onBlur={validateFields} />
                                        <label className="form-label floating-label" htmlFor="phone">Phone</label>
                                    </div>
                                    <div className="validation-error">{errors.phone}</div>

                                    <div className="form-outline">
                                        <input type="password" id="password" name="password" className={"form-control form-control-lg" + (errors.password ? outlineRed : "")}
                                            onChange={handleChange} value={details.password} onBlur={validateFields} />
                                        <label className="form-label floating-label" htmlFor="password">Password</label>
                                    </div>
                                    <div className="validation-error">{errors.password}</div>

                                    <div className="form-outline">
                                        <input type="password" id="confirm-password" name="confirm_password" className={"form-control form-control-lg" + (errors.confirm_password ? outlineRed : "")}
                                            onChange={e => setConfirmPassword(e.currentTarget.value)} value={confirm_password} onBlur={validateFields} />
                                        <label className="form-label floating-label" htmlFor="confirm-password">Confirm Password</label>
                                    </div>
                                    <div className="validation-error">{errors.confirm_password}</div>

                                    <div className="d-flex justify-content-center mb-4">
                                        <div className="g-recaptcha" data-sitekey={siteKey}></div>
                                    </div>
                                    <div>
                                        <button className="btn btn-primary btn-block w-100" type="submit">Register</button>
                                    </div>

                                </form>

                                <hr className="my-4" />
                                <div className="d-flex justify-content-between">
                                    <span>Already have an account?</span>
                                    <Link to={'/login'} className="link-primary">Sign in here</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}
