import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Card, AddCard, AddSocietyModal, EditSocietyModal, DeleteModal } from '../../components/components';
import axios from 'axios';

import './SocietyList.css';

export default function SocietyList({ dependencies: [setModalContent, setSpinner, setAlert] }) {
    const [societies, setSocieties] = useState([]);

    const getSocieties = () => {
        setSpinner(true);
        axios.get('getSocieties').then(
            res => {
                setSocieties(res.data);
                setSpinner(false);
            },
            err => {
                console.log(err);
                setSpinner(false);
            }
        );
    };

    const getNamedId = (name, id) => {
        return `${name.toLowerCase().replace(" ", "-")}-${id}`;
    };

    useEffect(() => {
        getSocieties();
    }, []);

    return (
        <div className="row m-0 justify-content-center" onClick={e => {
            if (!e.target.classList.contains("dropdown")) {
                [...document.getElementsByClassName("dropdown-menu")].forEach(el => el.style.display = "none");
            }
        }}>
            <h3 className="d-flex justify-content-center">
                <span className='text-watermark'>Societies</span>
            </h3>
            {societies && societies.map(society => {
                return (
                    <Card key={society.id} id={society.id}
                        header={<div><Link
                            to={`/societies/${getNamedId(society.name, society.id)}`}>
                            {society.name}
                        </Link>
                        </div>}
                        body={<div className="text-ellipsis--2">{society.address} - {society.pin_code}</div>}
                        footer={<>
                            <i className="btn fa fa-edit text-primary"
                                onClick={() => setModalContent(<EditSocietyModal
                                    initialDetails={{
                                        id: society.id, name: society.name,
                                        address: society.address, pin_code: society.pin_code,
                                        countryId: society.countryId
                                    }}
                                    setModalContent={setModalContent}
                                    setAlert={setAlert}
                                    getSocieties={getSocieties} />)}></i>

                            <div role="button" className="btn btn-secondary dropdown py-0" onClick={e => {
                                const dropdownMenu = e.currentTarget.getElementsByClassName("dropdown-menu")[0];
                                dropdownMenu.style.display = dropdownMenu.style.display === "none" ? "block" : "none";
                            }}>
                                <div className="dropdown" role="button">
                                    <i className="fas fa-ellipsis-h dropdown"></i>
                                </div>
                                <div className="dropdown-menu" style={{ marginLeft: "-4rem" }}>
                                    <Link className="dropdown-item"
                                        to={`/societies/${getNamedId(society.name, society.id)}/users`}>
                                        Users
                                    </Link>
                                    <Link className="dropdown-item"
                                        to={`/societies/${getNamedId(society.name, society.id)}/collections`}>
                                        Collections
                                    </Link>
                                    <Link className="dropdown-item"
                                        to={`/societies/${getNamedId(society.name, society.id)}/transactions`}>
                                        Transactions
                                    </Link>
                                </div>
                            </div>
                            <i className="btn fas fa-trash-alt text-danger"
                                onClick={() => setModalContent(<DeleteModal
                                    deleteUrl={`deleteSociety/${society.id}`}
                                    entityName={society.name}
                                    setModalContent={setModalContent}
                                    setAlert={setAlert}
                                    getEntities={getSocieties} />)}></i>
                        </>
                        }></Card>
                );
            })}
            <AddCard setModalContent={setModalContent} setAlert={setAlert}
                modalContent={<AddSocietyModal
                    setModalContent={setModalContent}
                    setAlert={setAlert}
                    getSocieties={getSocieties} />} />
        </div>
    );
}