import React, { useEffect, useState } from 'react';
import { Navigate } from 'react-router-dom';

import '../../../../pages/UserForms/Forms.css';

import { setDependencies, societyModalBodyTemplate, submitHandler, getCountries } from './SocietyModals';

export default function AddSocietyModal({ setModalContent, setAlert, getSocieties }) {
    const [details, setDetails] = useState({ name: "", address: "", pin_code: "", countryId: "" });
    const [errors, setErrors] = useState({});

    const [countries, setCountries] = useState([]);

    setDependencies([setModalContent, setAlert, getSocieties, 
        setDetails, setErrors, setCountries, details, errors, countries]);
    useEffect(getCountries, []);

    return (alert && alert.class==="alert-success")?(<Navigate to={'/'}/>):(<div className="modal-content">
        <div className="modal-header">
            <h5 className="modal-title">Add Society</h5>
            <button type="button" className="btn-close" onClick={() => setModalContent(null)}>
            </button>
        </div>
        <form onSubmit={e=>submitHandler(e, "addSociety", "POST")}>
            {societyModalBodyTemplate()}
            <div className="form-outline modal-footer d-flex justify-content-between">
                <button type="submit" className="btn btn-primary">Add</button>
                <button type="button" className="btn btn-primary" onClick={() => setModalContent(null)}>Cancel</button>
            </div>
        </form>
    </div>);
}
