import axios from 'axios';
import React, { useState } from 'react';
import ResetPassword from './ResetPassword';

export default function AnswerSecurityQuestions({ email, userQuestions, setAlert }) {
    const [securityQuestions, setSecurityQuestions] = useState([...userQuestions]);
    const [errors, setErrors] = useState({});
    const [token, setToken] = useState("");

    const defaultErr = "Please enter a value";

    const submitHandler = e => {
        e.preventDefault();
        for (const item in securityQuestions) {
            if (!securityQuestions[item]) {
                errors[item] = defaultErr;
            }
        }
        for (const item in errors) {
            if (errors[item]) {
                setAlert({ class: "alert-danger", message: "Please fill required securityQuestions" });
                return;
            }
        }

        axios.post('verifySecurityQuestions', {
            questions: securityQuestions.map(q => { return { id: q.id, answer: q.answer }; }),
            email: email
        }).then(
            res => setToken(res.data.token),
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
            }
        );
    };

    const validateFields = e => {
        const { name, value } = e.target;
        if (value === "") {
            setErrors({ ...errors, [name]: defaultErr });
            return;
        }
        setErrors({ ...errors, [name]: "" });
    };
    
    if(token){
        return <ResetPassword token={token}
        setAlert={setAlert} />;
    }

    return (
        <div className="p-3">
            <h5 className="pb-2">Answer Security Questions</h5>
            <form className="form-horizontal" onSubmit={submitHandler}>

                <div className="form-outline">
                    <input className="form-control form-control-lg securityAnswer" name="answer1"
                        onChange={e => { securityQuestions[0].answer = e.currentTarget.value; setSecurityQuestions([...securityQuestions]); }}
                        value={securityQuestions[0].answer}
                        onBlur={validateFields}>
                    </input>
                    <label className="form-label floating-label">{securityQuestions[0].question}</label>
                </div>
                <div className="validation-error">{errors.answer1}</div>

                <div className="form-outline">
                    <input className="form-control form-control-lg securityAnswer" name="answer2"
                        onChange={e => { securityQuestions[1].answer = e.currentTarget.value; setSecurityQuestions([...securityQuestions]); }}
                        value={securityQuestions[1].answer}
                        onBlur={validateFields}>
                    </input>
                    <label className="form-label floating-label">{securityQuestions[1].question}</label>
                </div>
                <div className="validation-error">{errors.answer2}</div>

                <div className="d-flex justify-content-end">
                    <button className="btn btn-primary" type="submit">Submit</button>
                </div>
            </form>
        </div>
    );
}
