import React, { useEffect, useState } from 'react';
import { setDependencies, submitHandler, setDetailsForExpenseTransaction, getExpenseModalBodyTemplate } from './ExpenseModal';

export default function EditExpenseModal({ expenseId, setModalContent, setAlert, getExpenses }) {
  const [details, setDetails] = useState({
    id: expenseId,
    amount: "",
    senderId: "",
    receiverId: "",
    date: "",
    methodId: "",
    transactionComment: ""
  });
  const [errors, setErrors] = useState({});
  const [senderAccounts, setSenderAccounts] = useState([]);
  const [receiverAccounts, setReceiverAccounts] = useState([]);
  const [paymentMethods, setPaymentMethods] = useState([]);

  setDependencies([setModalContent, setAlert, getExpenses, setDetails, setErrors,
    setSenderAccounts, setReceiverAccounts, setPaymentMethods, details,
    errors, senderAccounts, receiverAccounts, paymentMethods]);

  useEffect(() => {
    setDetailsForExpenseTransaction(expenseId);
  }, []);

  return (
    <div className="modal-content">
      <div className="modal-header">
        <h5 className="modal-title">Edit Expense</h5>
        <button type="button" className="btn-close" onClick={() => setModalContent(null)}>
        </button>
      </div>
      <form onSubmit={e=> submitHandler(e, "updateExpense", "PUT")}>
        {getExpenseModalBodyTemplate()}
        <div className="form-outline modal-footer d-flex justify-content-between">
          <button type="submit" className="btn btn-primary">Update</button>
          <button type="button" className="btn btn-primary" onClick={() => setModalContent(null)}>Cancel</button>
        </div>
      </form>
    </div>
  );
}
