import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { AddExpenseModal, DeleteModal, EditExpenseModal, ViewExpenseModal } from '../../components/components';

import './Expenses.css';

export default function Expenses({ dependencies: [setModalContent, setSpinner, setAlert] }) {
  const { collectionId } = useParams();
  const [expenseData, setExpenseData] = useState({
    currency: "",
    totalExpenditure: "",
    expenses: []
  });

  const getExpenses = () => {
    setSpinner(true);
    axios.get(`getExpenses/${collectionId}`).then(
      res => {
        setExpenseData(res.data);
        setSpinner(false);
      },
      err => {
        if (err.response.data.message !== undefined) {
          setAlert({ class: "alert-danger", message: err.response.data.message });
        }
        else {
          setAlert({ class: "alert-danger", message: err.response.data });
        }
        setSpinner(false);
      }
    );
  };
  useEffect(getExpenses, []);
  return (
    <>
      <h3 className="d-flex justify-content-center">
        <span className='text-watermark'>Expenses</span>
      </h3>
      <div className="card shadow mx-3 table-responsive">
        {expenseData.expenses.length > 0 ?
          <table className="table table-striped mb-2">
            <thead>
              <tr>
                <th scope="col">Expense Head</th>
                <th scope="col">Expense Amount</th>
                <th scope="col">Action</th>
              </tr>
            </thead>
            <tbody>
              {expenseData.expenses.map((expense, i) => {
                return <tr key={i}>
                  <td><div role="button" className="shadow btn-sm btn-primary"
                    onClick={() => setModalContent(<ViewExpenseModal
                      expenseId={expense.id}
                      setModalContent={setModalContent}
                    />)}>
                    {expense.expenseEntity}</div></td>
                  <td><div className="btn-sm btn-danger">{expense.expenseAmount}</div></td>
                  <td><i role="button" className="btn-sm fa fa-edit text-primary"
                    onClick={() => setModalContent(<EditExpenseModal
                      expenseId={expense.id}
                      setModalContent={setModalContent}
                      setAlert={setAlert}
                      getExpenses={getExpenses} />)}
                  ></i>
                    <i role="button" className="btn-sm fa fa-trash-alt text-danger"
                      onClick={() => setModalContent(<DeleteModal
                        deleteUrl={`deleteExpense/${expense.id}`}
                        entityName={"expense on " + expense.expenseEntity}
                        setModalContent={setModalContent}
                        setAlert={setAlert}
                        getEntities={getExpenses} />)}
                    ></i></td>
                </tr>;
              })

              }
              <tr className="bg-dark">
                <td className="text-white">Total</td>
                <td className="text-white">{expenseData.totalExpenditure}</td>
                <td></td>
              </tr>
            </tbody>
          </table> : <div className="card-body text-center">No expenses yet</div>
        }
      </div>
      <div className="text-center">
        <button type="button" className="btn btn-primary encircle-lg"
          onClick={() => setModalContent(<AddExpenseModal
            collectionId={collectionId}
            setModalContent={setModalContent}
            setAlert={setAlert}
            getExpenses={getExpenses} />)}>
          <i className="fa fa-plus"></i>
        </button>
      </div>
    </>
  );
}
