import React from 'react';

export default function Card({ id, header, body, footer }) {
    return <div key={id} className="p-3" style={{ maxWidth: "26.3rem" }}>
        <div className="card shadow d-inline-block align-top w-100" style={{ height: "12rem" }}>
            <h4 className="card-header font-weight-bold text-center text-truncate">
                {header}
            </h4>
            <div className="card-body d-flex flex-column justify-content-center" style={{ height: "5.7rem" }}>
                {body}
            </div>
            <div className="card-footer position-absolute w-100 bottom-0
             bg-white d-flex align-items-center justify-content-around">
                {footer}
            </div>
        </div>
    </div>;
}
