import React, { useState, useEffect } from 'react';

import './Loading.css';

export default function Loading({ spinner }) {
    const [intervalId, setIntervalId] = useState(0);
    const getRandomNumber = range => {
        return Math.floor(Math.random() * range);
    };

    useEffect(() => {
        if (spinner) {
            const el = document.getElementById("spinner");
            const arorizeCircle = (el, range) => {
                let r = getRandomNumber(range);
                let g = getRandomNumber(range);
                let b = getRandomNumber(range);
                el.style.color = `rgb(${r},${g},${b})`;
                el.style.boxShadow = `0px 0px 91px rgb(${r},${g},${b})`;
            };
            arorizeCircle(el, 255);
            setIntervalId(setInterval(arorizeCircle, 1000, el, 255));
        }
    }, [spinner]);

    return (
        spinner ? (
            <div>
                <div className="loading-container">
                    <div id="spinner">
                        <span>Loading...</span>
                    </div>
                </div>
                <div className="loading-backdrop"></div>
            </div>
        ) :
            (<>{clearInterval(intervalId)}</>)
    );
}
