import React, { useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import { Card } from '../../components/components';
import axios from 'axios';

export default function CollectionSummary({ dependencies: [setSpinner, setAlert] }) {
    const [summary, setSummary] = useState({
        currency: "",
        collectionName: "",
        incomeSummary: {
            totalExpected: "",
            totalReceived: "",
            remaining: ""
        },
        expenseSummary: {
            totalExpenditure: "",
            previousBalance: "",
            balance: ""
        }
    });
    const { collectionId } = useParams();
    const cardTxtClassList = "d-flex align-content-center justify-content-between w-100 py-1";

    const getCollectionSummary = () => {
        setSpinner(true);
        axios.get(`getCollectionSummary/${collectionId}`).then(
            res => {
                setSummary(res.data);
                setSpinner(false);
            },
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
                setSpinner(false);
            }
        );
    };

    useEffect(() => {
        getCollectionSummary();
    }, []);

    return (
        <div className="row m-0 justify-content-center">
            <h3 className="d-flex justify-content-center">
                <span className='text-watermark'>Collection Summary</span>
            </h3>
            <Card
                header={<div><Link
                    to={"incomes"}>
                    Incomes
                </Link>
                </div>}
                body={<>
                    <div className={cardTxtClassList}>
                        <span className="fw-bold">Total Expected:</span>
                        <span className="btn-sm btn-primary">{summary.currency} {summary.incomeSummary.totalExpected}</span>
                    </div>
                    <div className={cardTxtClassList}>
                        <span className="fw-bold">Total Received:</span>
                        <span className={`btn-sm ${summary.incomeSummary.totalReceived > 0 ? "btn-success" : "btn-danger"}`}>
                            {summary.currency} {summary.incomeSummary.totalReceived}</span>
                    </div>
                </>}
                footer={<div className={cardTxtClassList}>
                    <span className="fw-bold">Remaining:</span>
                    <span className={`btn-sm ${summary.incomeSummary.remaining <= 0 ? "btn-success" : "btn-danger"}`}>
                        {summary.currency} {summary.incomeSummary.remaining}</span>
                </div>
                } />
            <Card
                header={<div><Link
                    to={"expenses"}>
                    Expenses
                </Link>
                </div>}
                body={<>
                    <div className={cardTxtClassList}>
                        <span className="fw-bold">Total Expenditure:</span>
                        <span className="btn-sm btn-primary">{summary.currency} {summary.expenseSummary.totalExpenditure}</span>
                    </div>
                    <div className={cardTxtClassList}>
                        <span className="fw-bold">Previous Balance:</span>
                        <span className={`btn-sm ${summary.expenseSummary.previousBalance > 0 ? "btn-success" : "btn-danger"}`}>
                            {summary.currency} {summary.expenseSummary.previousBalance}</span>
                    </div>
                </>}
                footer={<div className={cardTxtClassList}>
                    <span className="fw-bold">Balance:</span>
                    <span className={`btn-sm ${summary.expenseSummary.balance > 0 ? "btn-success" : "btn-danger"}`}>
                        {summary.currency} {summary.expenseSummary.balance}</span>
                </div>
                } />
        </div>
    );
}
