import React , { useEffect } from 'react';

export default React.memo(function Modal({modalContent}) {

    useEffect(()=>{
        const el = document.getElementsByClassName("modal")[0];
        if(modalContent){
            el.style.display="block";
            setTimeout(()=>el.classList.add("show"), 300);
        }
        else{
            el.classList.remove("show");
            setTimeout(()=> el.style.display="none", 300);
        }
    }, [modalContent]);

    return (
        <div id="modal" className={modalContent?"modal-open": ""}>
            <div className="modal fade" tabIndex="-1">
                <div className="modal-dialog" role="document">
                    {modalContent}
                </div>
            </div>
            {modalContent?(<div className="modal-backdrop fade show"></div>):(<></>)}
        </div>);
});
