import React, { useState, useRef, useEffect } from 'react';
import { Navigate } from 'react-router-dom';

import { setDependencies, getFlatMembers } from './FlatModals';

import '../../../../pages/UserForms/Forms.css';
import './FlatModals.css';

export default function ViewFlatModal({ initialDetails, setModalContent }) {
    const [details, setDetails] = useState(initialDetails);
    const users = useRef([]);
    const [errors, setErrors] = useState({});
    const [selectedFlatMembers, setSelectedFlatMembers] = useState([]);
    const [userSuggestions, setUserSuggestions] = useState([]);

    setDependencies([setDetails, setErrors, setUserSuggestions, setSelectedFlatMembers,
        setModalContent, null, null, userSuggestions, selectedFlatMembers, details, users, errors]);

    const userSelectViewTemplate = (user) => {
        return <div key={selectedFlatMembers.length}
            className="form-check d-flex align-items-center justify-content-between">
            <input className="form-check-input" title="Selected Owner" type="radio" name="members"
                checked={selectedFlatMembers.length === 0 ? true : false} readOnly/>
            <input list="user-suggestions" type="text" className="form-control w-100"
                value={`${user.name} (${user.email})`} disabled />
        </div>;
    };

    useEffect(() => {
        getFlatMembers(initialDetails.id, userSelectViewTemplate);
    }, []);

    return (alert && alert.class === "alert-success") ? (<Navigate to={'/'} />) : (<div className="modal-content">
        <div className="modal-header">
            <h5 className="modal-title">View Flat</h5>
            <button type="button" className="btn-close" onClick={() => setModalContent(null)}>
            </button>
        </div>
        <div className="modal-body">
            <div className="form-outline mb-4">
                <input type="text" className="form-control form-control-lg" name="code"
                    value={details.code} disabled />
                <label className="form-label floating-label">Flat Code</label>
            </div>
            <div className="form-outline mb-4">
                <input type="number" className="form-control form-control-lg hidden-arrows" name="area"
                    value={details.area} disabled />
                <label className="form-label floating-label">Area</label>
            </div>
            <div>
                <div>
                    <strong>Members</strong>
                </div>
                <div id="selected-flat-members" >
                    {selectedFlatMembers.length>0?selectedFlatMembers:<small>No members yet</small>}
                </div>
            </div>
        </div>
        <div className="form-outline modal-footer d-flex justify-content-end">
            <button type="button" className="btn btn-primary" onClick={() => setModalContent(null)}>Cancel</button>
        </div>
    </div>);
}