import React, { useEffect } from 'react';

export default function About({dependencies: [setSpinner]}) {

    useEffect(()=>{
        setSpinner(false);
    });
    
  return (
    <div className="card shadow mx-3">
        <div className="card-body">
            <p className="p-3">Helps to manage societies, flats, members and collections</p>
            <hr />
            <h5>Steps to start with:</h5>
            <ul>
                <li>Sign up and login to the application</li>
                <li>Add a society using '+' button</li>
                <li>Now add users in the society using (...) option present on card</li>
                <li>Navigate to the society by clicking on the title</li>
                <li>Add flats in society using '+' button</li>
                <li>Add owner and members inside flat by click on edit icon</li>
                <li>Once flats and their owners are added, navigate to collections section using (...) on society card</li>
                <li>Select appropriate collection type and add collections</li>
                <li>Navigate to collection by clicking on title</li>
                <li>Mangage from income section, expected income amount will already be added based on collection type</li>
                <li>Add expenses which took place for that collection</li>
            </ul>
            <p className="pt-5 text-center">version 1.0.0</p>
        </div>
    </div>
  );
}
