import React, { useState, useEffect } from 'react';
import { setDependencies, getUsers } from './SocietyUserModal';

export default function ViewSocietyUserModal({ initialDetails, setModalContent }) {
    const [details, setDetails] = useState(initialDetails);
    const [users, setUsers] = useState([]);

    setDependencies([setDetails, setUsers, null, null, null,
        details, users, null]);

    useEffect(() => {
        getUsers();
    }, []);

    return (
        <div className="modal-content">
            <div className="modal-header">
                <h5 className="modal-title">View User</h5>
                <button type="button" className="btn-close" onClick={() => setModalContent(null)}>
                </button>
            </div>
                <div className="modal-body">
                    <div className="form-outline mb-4">
                        <input type="text" className="form-control form-control-lg" name="email"
                            value={details.email} disabled/>
                        <label className="form-label floating-label">Email ID</label>
                    </div>
                    <div className="form-outline mb-4">
                        <input type="text" className="form-control form-control-lg" name="name"
                            value={details.name} disabled />
                        <label className="form-label floating-label">Name</label>
                    </div>
                    <div className="form-check d-flex justify-content-start mb-4">
                        <input
                            className="form-check-input"
                            name="isAdmin"
                            type="checkbox"
                            checked={details.isAdmin}
                            id="isAdmin"
                            disabled
                        />
                        <label className="form-check-label" htmlFor="isAdmin"
                            style={{ padding: '0rem 1rem' }}>Admin</label>
                    </div>
                </div>
                <div className="form-outline modal-footer d-flex justify-content-end">
                    <button type="button" className="btn btn-primary" onClick={() => setModalContent(null)}>Cancel</button>
                </div>
        </div>
    );
}
