import React from 'react';

export default function AddCard({ setModalContent, modalContent, setAlert }) {
    return <div className="p-3" style={{ maxWidth: "26.3rem" }} title="Add Society">
        <div className="card shadow d-inline-block align-top w-100" style={{ height: "12rem" }}>
            <div className="display-1 text-center h-100 d-flex align-items-center justify-content-center" style={{ color: "rgb(200, 200, 200)" }}>
                <i className="fas fa-plus" onClick={() => setModalContent(modalContent)} style={{ cursor: "pointer" }}></i>
            </div>
        </div>
    </div>;
}
