import React, { useEffect, useState } from 'react';
import AnswerSecurityQuestions from './AnswerSecurityQuestions';
import axios from 'axios';

export default function ForgotPassword({ dependencies: [setSpinner, setAlert] }) {

    const [details, setDetails] = useState({ email: "" });
    const [errors, setErrors] = useState({});
    const [userQuestions, setUserQuestions] = useState([]);

    const defaultErr = "Please enter a value";

    const submitHandler = e => {
        e.preventDefault();
        for (const item in details) {
            if (!details[item]) {
                errors[item] = defaultErr;
            }
        }
        for (const item in errors) {
            console.log(errors);
            if (errors[item]) {
                setAlert({ class: "alert-danger", message: "Please fill required details" });
                return;
            }
        }

        axios.post('getUserQuestions', details).then(
            res => {
                setUserQuestions(res.data);
            },
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
            }
        );
    };

    const validateFields = e => {
        const { dataset, value } = e.target;
        const name = dataset.name;
        if (value === "") {
            setErrors({ ...errors, [name]: defaultErr });
            return;
        }

        else if (name === "email") {
            let validEmail = /^\S+@\S+\.\S+$/;
            if (!validEmail.test(value)) {
                setErrors({ ...errors, [name]: "Please enter a valid email id" });
                return;
            }
        }
        setErrors({ ...errors, [name]: "" });
    };

    useEffect(() => {
        setSpinner(false);
    }, []);

    return (
        <div className="container p-3">
            <div className='card shadow col-12 col-md-8 col-lg-6 col-xl-5 m-auto'>
                {(() => {
                    if (userQuestions.length === 0) {
                        return <div className="card-body">
                            <h6 className="pb-2">Please enter your email id</h6>
                            <form onSubmit={submitHandler}>
                                <div className="form-outline mb-4">
                                    <input type="email" className="form-control form-control-lg" data-name="email"
                                        onChange={e => setDetails({ ...details, email: e.currentTarget.value })}
                                        value={details.email}
                                        onBlur={validateFields}>
                                    </input>
                                    <label className="form-label floating-label">Email ID</label>
                                    <div className="validation-error">{errors.email}</div>
                                </div>
                                <div className="d-flex justify-content-end">
                                    <button className="btn btn-primary" type="submit">Submit</button>
                                </div>
                            </form>
                        </div>;
                    }
                    else {
                        return <AnswerSecurityQuestions
                            email={details.email}
                            setAlert={setAlert}
                            userQuestions={[...userQuestions]}
                        ></AnswerSecurityQuestions>;
                    }
                })()}
            </div>
        </div>
    );
}
