import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import { getLongDate } from '../../others/helper';

export default function Transactions({ dependencies: [setSpinner, setAlert] }) {
    const { societyId } = useParams();
    const [details, setDetails] = useState([]);

    useEffect(() => {
        setSpinner(true);
        axios.get(`getTransactions/${societyId}`).then(
            res => {
                setDetails(res.data);
                setSpinner(false);
            },
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
                setSpinner(false);
            }
        );
    }, []);
    return (
        <>
            <h3 className="d-flex justify-content-center">
                <span className='text-watermark'>Transactions</span>
            </h3>
            <div className="table-responsive card shadow mx-3">
                {details.length > 0 ?
                    <table className="table table-striped mb-2">
                        <thead>
                            <tr>
                                <th scope="col">ID</th>
                                <th scope="col">Sender</th>
                                <th scope="col">Receiver</th>
                                <th scope="col">Amount</th>
                                <th scope="col">Date</th>
                                <th scope="col">Payment Method</th>
                                <th scope="col" className="px-5">Comment</th>
                            </tr>
                        </thead>
                        <tbody>
                            {details.map(transaction => {
                                return <tr key={transaction.public_id}>
                                    <td>{transaction.public_id}</td>
                                    <td>{transaction.sender.name + (
                                        transaction.sender.flatCode ? `(${transaction.sender.flatCode})` : ``)}</td>
                                    <td>{transaction.receiver.name + (
                                        transaction.receiver.flatCode ? `(${transaction.receiver.flatCode})` : ``)}</td>
                                    <td>{transaction.amount}</td>
                                    <td>{getLongDate(transaction.date)}</td>
                                    <td>{transaction.paymentMethod}</td>
                                    <td>{transaction.transactionComment}</td>
                                </tr>;
                            })
                            }
                        </tbody>
                    </table> : <div className="card-body text-center">No transactions yet</div>}
            </div>
        </>
    );
}
