import axios from 'axios';

import { getLongDate } from '../../../../others/helper';

let setModalContent, setAlert, getSocietyCollections, setDetails, setErrors, setCollectionTypes,
    details, errors, collectionTypes;
const setDependencies = dependencyArray => {
    [setModalContent, setAlert, getSocietyCollections, setDetails, setErrors, setCollectionTypes,
        details, errors, collectionTypes] = dependencyArray;
};

const submitHandler = (e, url, method) => {
    e.preventDefault();
    for (const item in details) {
        if (details[item] === "") {
            setAlert({ class: "alert-danger", message: "Please fill all the fields" });
            return;
        }
    }
    for (const item in errors) {
        if (errors[item] !== "") {
            setAlert({ class: "alert-danger", message: "Please fill correct details" });
            return;
        }
    }
    axios({
        url: url,
        method: method,
        data: details
    }).then(
        res => {
            setModalContent(null);
            getSocietyCollections();
            setAlert({ class: "alert-success", message: res.data.message });
        },
        err => {
            if (err.response.data.message !== undefined) {
                setAlert({ class: "alert-danger", message: err.response.data.message });
            }
            else {
                setAlert({ class: "alert-danger", message: err.response.data });
            }
        }
    );
};

const getCollectionTypes = () => {
    axios.get('getCollectionTypes').then(
        res => {
            setCollectionTypes(res.data);
        },
        err => console.log(err)
    );
};

const handleChange = e => {
    const { name, value, checked } = e.target;
    if (name === "fixed") {
        setDetails({ ...details, [name]: checked });
    }
    else {
        setDetails({ ...details, [name]: value });
    }
};

const getCurrentMonthYearLabel = () => {
    let options = { year: 'numeric', month: 'short' };
    let date = new Date();
    let output = date.toLocaleDateString('en-UK', options);
    return output;
};

const validateFields = e => {
    const { name, value } = e.target;
    if (value === "") {
        setErrors({ ...errors, [name]: "Please enter a value" });
        return;
    }
    setErrors({ ...errors, [name]: "" });
};

const collectionModalBodyTemplate = () => {
    return <div className="modal-body">
        <div className="form-outline">
            <select className="form-select form-select-lg" name="typeId" data-value={details.typeId}
                onChange={e => {
                    if (e.currentTarget.value === "1") {
                        setDetails({ ...details, typeId: e.currentTarget.value, name: `Monthly collection ${getCurrentMonthYearLabel()}` });
                    }
                    else{
                        setDetails({ ...details, typeId: e.currentTarget.value, fixed: true });
                    }
                }} value={details.typeId} onBlur={validateFields}>
                <option style={{ display: "none" }}></option>
                {collectionTypes.map((item, i) => <option key={i} value={item.id}>{item.type}</option>)}
            </select>
            <label className="form-label floating-label">Type</label>
        </div>
        <div className="validation-error">{errors.typeId}</div>
        <div className="form-outline">
            <input type="text" className="form-control form-control-lg" name="name"
                onChange={handleChange} onBlur={validateFields}
                value={details.name} />
            <label className="form-label floating-label">Name</label>
        </div>
        <div className="validation-error">{errors.name}</div>
        <div className="form-outline">
            <input type="date" className="form-control form-control-lg datepicker" name="collectionStartDate"
                onChange={handleChange} value={details.collectionStartDate} onBlur={validateFields} max={(new Date()).toISOString().split("T")[0]}/>
            <label className="date-label">{getLongDate(details.collectionStartDate)}</label>
            <label className="form-label floating-label">Start Date</label>
        </div>
        <div className="validation-error">{errors.collectionStartDate}</div>
        <div className="form-outline">
            <input type="number" className="form-control form-control-lg hidden-arrows" name="rate"
                onChange={handleChange} value={details.rate} onBlur={validateFields} />
            <label className="form-label floating-label">Rate</label>
        </div>
        <div className="validation-error">{errors.rate}</div>
        <div className="form-check d-flex justify-content-start mb-4">
            <input
                className="form-check-input"
                name="fixed"
                type="checkbox"
                onChange={handleChange}
                checked={details.fixed}
            />
            <label className="form-check-label"
                style={{ padding: '0rem 1rem' }}>Fixed</label>
        </div>
    </div>;
};

export { setDependencies, submitHandler, collectionModalBodyTemplate, getCollectionTypes };