const stringContainsNumber = string => {
    let string1 = String(string);
    for (let i = 0; i < string1.length; i++) {
        if (!isNaN(string1.charAt(i)) && string1.charAt(i) !== " ") {
            return true;
        }
    }
    return false;
};

const getLongDate = dateString => {
    let options = { year: 'numeric', month: 'short', day: 'numeric' };
    let date = new Date(dateString);
    let output = date.toLocaleDateString('en-UK', options);
    return output !== "Invalid Date" ? output : "";
};

export { stringContainsNumber, getLongDate };