import axios from 'axios';
import React, { useState } from 'react';
import { Navigate } from 'react-router-dom';

export default function ResetPassword({ token, setAlert }) {
    const [details, setDetails] = useState({ password: "" });
    const [confirmPassword, setConfirmPassword] = useState('');
    const [errors, setErrors] = useState({});
    const [isResetSuccess, setIsResetSuccess] = useState(false);
    const defaultErr = "Please enter a value";
    const outlineRed = " outline-red";

    const submitHandler = e => {
        e.preventDefault();
        for (const item in details) {
            if (!details[item]) {
                errors[item] = defaultErr;
            }
        }
        for (const item in errors) {
            if (errors[item]) {
                setAlert({ class: "alert-danger", message: "Please fill required details" });
                return;
            }
        }

        axios({
            url: "/resetPassword",
            method: "POST",
            headers: { Authorization: "Bearer " + token },
            data: details
        }).then(
            res => {
                setAlert({ class: "alert-success", message: res.data.message });
                setIsResetSuccess(true);
            },
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
            }
        );
    };

    const handleChange = e => {
        const { name, value } = e.target;
        if (name !== "confirm_password") {
            setDetails({ ...details, [name]: value });
        }
    };

    const validateFields = e => {
        const { name, value } = e.target;
        if (value === "") {
            setErrors({ ...errors, [name]: defaultErr });
            return;
        }
        else if (name === "confirm_password" && details.password !== confirmPassword) {
            setErrors({ ...errors, [name]: "Password do not match" });
            return;
        }
        setErrors({ ...errors, [name]: "" });
    };

    if(isResetSuccess){
        return <Navigate to={"/login"}/>;
    }

    return (
        <div className="p-3">
            <h5 className="pb-2">Reset Password</h5>
            <form className="form-horizontal" onSubmit={submitHandler}>

                <div className="form-outline">
                    <input type="password" name="password" className={"form-control form-control-lg" + (errors.password ? outlineRed : "")}
                        onChange={handleChange} value={details.password} onBlur={validateFields} />
                    <label className="form-label floating-label" >New Password</label>
                </div>
                <div className="validation-error">{errors.password}</div>

                <div className="form-outline">
                    <input type="password" name="confirm_password" className={"form-control form-control-lg" + (errors.confirm_password ? outlineRed : "")}
                        onChange={e => setConfirmPassword(e.currentTarget.value)} value={confirmPassword} onBlur={validateFields} />
                    <label className="form-label floating-label" >Confirm New Password</label>
                </div>
                <div className="validation-error">{errors.confirm_password}</div>
                <div className="d-flex justify-content-end">
                    <button className="btn btn-primary" type="submit">Reset</button>
                </div>
            </form>
        </div>
    );
}
