import React, { useEffect } from 'react';

import './Alert.css';

export default React.memo(function Alert({alert, setAlert}) {

    useEffect(()=>{
        if(alert.class){
            setTimeout(setAlert, 3000, { class: "", message: "" });
        }
    }, [alert.class]);

    return <div className={"text-center alert " + (alert && alert.class?alert.class:"")}>
        <span className="alert-msg">{alert && alert.message?alert.message:""}</span>
    </div>;
});