import React from 'react';
import { Navigate } from 'react-router-dom';
import axios from 'axios';

export default function DeleteModal({ deleteUrl, entityName, setModalContent, setAlert, getEntities}) {
    const submitHandler = e => {
        e.preventDefault();
        axios.delete(deleteUrl).then(
            res => {
                setModalContent(null);
                getEntities();
                setAlert({ class: "alert-success", message: res.data.message });
            },
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
            }
        );
    };

    return (alert && alert.class==="alert-success")?(<Navigate to={'/'}/>):(<div className="modal-content">
        <div className="modal-header">
            <h5 className="modal-title">Delete Society</h5>
            <button type="button" className="btn-close" onClick={() => setModalContent(null)}>
            </button>
        </div>
        <form onSubmit={submitHandler}>
            <div className="modal-body">
                <div>Are you sure you want to delete <strong>{entityName}</strong>?</div>
            </div>
            <div className="form-outline modal-footer d-flex justify-content-between">
                <button type="submit" className="btn btn-primary">Delete</button>
                <button type="button" className="btn btn-primary" onClick={() => setModalContent(null)}>Cancel</button>
            </div>
        </form>
    </div>);
}
