import React, { useState, useEffect } from 'react';
import { Navigate, Link } from "react-router-dom";
import axios from 'axios';

import './Forms.css';

export default function LoginForm({ dependencies: [getUser, user, setSpinner, setAlert] }) {
    const [details, setDetails] = useState({ username: "", password: "", remember_me: true });

    useEffect(() => {
        setSpinner(false);
        localStorage.clear();
    }, []);

    const submitHandler = e => {
        e.preventDefault();
        for (const item in details) {
            if (details[item] === "") {
                setAlert({ class: "alert-danger", message: "Please enter the details" });
                return;
            }
        }
        const config = {
            headers: {
                'Authorization': 'Basic ' + btoa(details.username + ":" + details.password),
                'Remember-Me': details.remember_me
            }
        };
        axios.post('loginUser', null, config).then(
            res => {
                localStorage.setItem('token', res.data.token);
                getUser();
            },
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
            }
        );
    };

    const handleChange = e => {
        const { name, value, checked } = e.target;
        if (name === "remember_me") {
            setDetails({ ...details, [name]: checked });
        }
        else {
            setDetails({ ...details, [name]: value });
        }
    };

    return (
        <>
            {
                user.user_full_name !== "" ? (<Navigate to={'/'} />) :
                    (

                        <section className="vh-100 w-100" style={{ backgroundColor: '#508bfc', marginTop: "-1rem" }}>
                            <div className="container py-5">
                                <div className="row d-flex justify-content-center align-items-center">
                                    <div className="col-12 col-md-8 col-lg-6 col-xl-5">
                                        <div className="card shadow-2-strong" style={{ borderRadius: '1rem' }}>
                                            <div className="card-body p-5 text-center">
                                                <h3 className="p-2">Sign in</h3>

                                                <form id="loginform" className="form-horizontal"
                                                    onSubmit={submitHandler}>
                                                    <div className="form-outline mb-4">
                                                        <input type="email" id="typeEmailX-2" name="username" className="form-control form-control-lg"
                                                            onChange={handleChange} value={details.username} />
                                                        <label className="form-label floating-label" htmlFor="typeEmailX-2">Email</label>
                                                    </div>

                                                    <div className="form-outline mb-4">
                                                        <input type="password" id="typePasswordX-2" name="password" className="form-control form-control-lg"
                                                            onChange={handleChange} value={details.password} />
                                                        <label className="form-label floating-label" htmlFor="typePasswordX-2">Password</label>
                                                    </div>
                                                    <div className="form-check d-flex justify-content-start mb-4">
                                                        <input
                                                            className="form-check-input"
                                                            name="remember_me"
                                                            type="checkbox"
                                                            onChange={handleChange}
                                                            checked={details.remember_me}
                                                            id="remember-check"
                                                        />
                                                        <label className="form-check-label" htmlFor="remember-check"
                                                            style={{ padding: '0rem 1rem' }}>Stay signed in for 7 days</label>
                                                    </div>

                                                    <div>
                                                        <button className="btn btn-primary btn-block w-100" type="submit">Login</button>
                                                    </div>

                                                </form>
                                                <hr className="my-4" />
                                                <div className="d-flex justify-content-between">
                                                    <Link to={'/signup'} className="link-primary">Sign up here</Link>
                                                    <Link to={'/forgot-password'} className="link-primary">Forgot Password?</Link>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </section>
                    )
            }
        </>
    );
}
