import React, { useRef, useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { Card, AddCard, DeleteModal, AddCollectionModal, EditCollectionModal } from '../../components/components';
import axios from 'axios';

import { getLongDate } from '../../others/helper.js';

export default function Collections({ dependencies: [setModalContent, setSpinner, setAlert] }) {
    const { societyId } = useParams();
    const [collections, setCollections] = useState([]);
    const currency = useRef("");
    const cardTxtClassList = "d-flex align-content-center justify-content-between w-100 py-1";

    const getSocietyCollections = () => {
        setSpinner(true);
        axios.get(`getCollections/${societyId}`).then(
            res => {
                currency.current = res.data.currency;
                setCollections(res.data.collections);
                setSpinner(false);
            },
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
                setSpinner(false);
            }
        );
    };

    useEffect(() => {
        getSocietyCollections();
    }, []);

    return (
        <div>
            <div className="d-flex justify-content-center position-relative">
                <h3 className="d-flex justify-content-center">
                    <span className='text-watermark'>Collections</span>
                </h3>
                <span className='badge rounded-pill bg-warning text-dark position-absolute top-0 end-0 me-2' style={{ height: "fit-content" }}>Count: {collections.length}</span>
            </div>
            <div className="row m-0 justify-content-center">
                {collections && collections.map(collection => {
                    return (
                        <Card key={collection.id} id={collection.id}
                            header={<Link to={`/collections/${collection.name.toLowerCase().replaceAll(" ", "-")}-${collection.id}`}>
                                {collection.name}
                            </Link>}
                            body={
                                <>
                                    <div className={cardTxtClassList}>
                                        <span className="fw-bold">Start Date:</span>
                                        <span className="btn-sm btn-primary">{getLongDate(collection.collectionStartDate)}</span>
                                    </div>
                                    <div className={cardTxtClassList}>
                                        <span className="fw-bold">Rate:</span>
                                        <span className="btn-sm btn-danger">
                                            {currency.current} {collection.rate} {collection.fixed ? "/ flat" : "/ sqft(flat)"}</span>
                                    </div>
                                </>}
                            footer={<>
                                <i className="btn col-auto glyphicon fa fa-edit text-primary"
                                    onClick={() => setModalContent(<EditCollectionModal
                                        initialDetails={{
                                            id: collection.id, name: collection.name, typeId: collection.typeId, rate: collection.rate,
                                            fixed: collection.fixed, collectionStartDate: collection.collectionStartDate
                                        }}
                                        setModalContent={setModalContent}
                                        setAlert={setAlert}
                                        getSocietyCollections={getSocietyCollections} />)}></i>
                                <i className="btn col-auto glyphicon fas fa-trash-alt text-danger"
                                    onClick={() => setModalContent(<DeleteModal
                                        deleteUrl={`deleteCollection/${collection.id}`}
                                        entityName={collection.name}
                                        setModalContent={setModalContent}
                                        setAlert={setAlert}
                                        getEntities={getSocietyCollections} />)}></i>
                            </>
                            }></Card>
                    );
                })}
                <AddCard setModalContent={setModalContent} setAlert={setAlert}
                    modalContent={<AddCollectionModal
                        societyId={societyId}
                        setModalContent={setModalContent}
                        setAlert={setAlert}
                        getSocietyCollections={getSocietyCollections} />} />
            </div>
        </div>
    );
}