import axios from 'axios';

let setDetails, setErrors, setUserSuggestions, setSelectedFlatMembers,
    setModalContent, setAlert, getSocietyFlats, userSuggestions, selectedFlatMembers, details, users, errors;
const setDependencies = dependencyArray => {
    [setDetails, setErrors, setUserSuggestions, setSelectedFlatMembers,
        setModalContent, setAlert, getSocietyFlats, userSuggestions, selectedFlatMembers, details, users, errors] = dependencyArray;
};

const getSocietyUsers = societyId => {
    axios.get(`getSocietyUsers/${societyId}`).then(
        res => {
            users.current = res.data;
        },
        err => console.log(err)
    );
};

const submitHandler = (e, url, methodName) => {
    e.preventDefault();
    for (const item in details) {
        if (details[item] === "" && item !== "owner") {
            setAlert({ class: "alert-danger", message: "Please fill all the fields" });
            return;
        }
    }
    for (const item in errors) {
        if (errors[item] !== "") {
            setAlert({ class: "alert-danger", message: "Please fill correct details" });
            return;
        }
    }
    const userSelectInputs = document.getElementsByClassName("user-select");
    details.members = [];
    for (let i = 0; i < userSelectInputs.length; i++) {
        if (users.current.filter(obj => obj.email === userSelectInputs[i].value).length === 1) {
            details.members.push(userSelectInputs[i].value);
        }
        else {
            setAlert({ class: "alert-danger", message: "Please select valid user(s)" });
            return;
        }
    }
    const formCheckInputs = document.getElementsByClassName("form-check-input");
    details.owner = "";
    for (let i = 0; i < formCheckInputs.length; i++) {
        if (formCheckInputs[i].checked) {
            details.owner = userSelectInputs[i].value;
            break;
        }
    }
    axios({
        url: url,
        method: methodName,
        data: details
    }).then(
        res => {
            setModalContent(null);
            getSocietyFlats();
            setAlert({ class: "alert-success", message: res.data.message });
        },
        err => {
            if (err.response.data.message !== undefined) {
                setAlert({ class: "alert-danger", message: err.response.data.message });
            }
            else {
                setAlert({ class: "alert-danger", message: err.response.data });
            }
        }
    );
};

const handleChange = e => {
    const { name, value } = e.target;
    setDetails({ ...details, [name]: value });
};

const validateFields = e => {
    const { name, value } = e.target;
    if (value === "") {
        setErrors({ ...errors, [name]: "Please enter a value" });
        return;
    }
    setErrors({ ...errors, [name]: "" });
};

const showUserSuggestions = e => {
    const userSelectInputs = document.getElementsByClassName("user-select");
    let suggestions = users.current.filter(obj => {
        for (let i = 0; i < userSelectInputs.length; i++) {
            if (userSelectInputs[i].value === obj.email) {
                return false;
            }
        }
        return true;
    });
    suggestions = suggestions.filter(obj => obj.name.toLowerCase().startsWith(e.currentTarget.value) ||
        obj.email.toLowerCase().startsWith(e.currentTarget.value) || !e.currentTarget.value
    );
    const suggestionList = suggestions.map(obj => {
        return <option key={obj.email} value={obj.email}>{obj.name}</option>;
    });
    setUserSuggestions([...suggestionList]);
};

const getFlatMembers = (id, template) => {
    template = template ? template : userSelectInputTemplate;
    axios(`getFlatMembers/${id}`).then(
        res => {
            if (res.data.owner) {
                selectedFlatMembers.push(template(res.data.owner));
                res.data.members.forEach((obj, i) => {
                    if (obj.email !== res.data.owner.email) {
                        selectedFlatMembers.push(template(obj));
                    }
                });
                setSelectedFlatMembers([...selectedFlatMembers]);
            }
        },
        err => {
            setAlert({ class: "alert-danger", message: err.response.data });
        }
    );
};

const userSelectInputTemplate = (user) => {
    return <div key={selectedFlatMembers.length}
        className="form-check d-flex align-items-center justify-content-between">
        <input className="form-check-input" title="Select Owner" type="radio" name="members"
            defaultChecked={selectedFlatMembers.length === 0 ? true : false} />
        <input list="user-suggestions" type="text" className="form-control user-select"
            placeholder="Search user"
            onKeyUp={showUserSuggestions}
            onFocus={showUserSuggestions}
            defaultValue={user ? user.email : ""}
        />
        <button type="button"
            data-index={selectedFlatMembers.length}
            className="encircle btn btn-danger"
            onClick={e => {
                selectedFlatMembers.splice(e.currentTarget.dataset.index, 1);
                setSelectedFlatMembers([...selectedFlatMembers]);
            }}>
            <i className="fa fa-minus"></i>
        </button>
    </div>;
};

const flatModalBodyTemplate = () => {
    return <div className="modal-body">
        <div className="form-outline">
            <input type="text" className="form-control form-control-lg" name="code"
                onChange={handleChange} value={details.code} onBlur={validateFields} />
            <label className="form-label floating-label">Flat Code</label>
        </div>
        <div className="validation-error">{errors.code}</div>
        <div className="form-outline">
            <input type="number" className="form-control form-control-lg hidden-arrows" name="area"
                onChange={handleChange} value={details.area} onBlur={validateFields} />
            <label className="form-label floating-label">Area(in sqft)</label>
        </div>
        <div className="validation-error">{errors.area}</div>
        <div>
            <div>
                <strong>Members</strong>
            </div>
            <datalist id="user-suggestions">
                {userSuggestions}
            </datalist>
            <div id="selected-flat-members" >
                {selectedFlatMembers}
            </div>
            <div>
                <button type="button" className="encircle btn btn-primary" onClick={() => {
                    selectedFlatMembers.push(userSelectInputTemplate());
                    setSelectedFlatMembers([...selectedFlatMembers]);
                }}>
                    <i className="fas fa-plus"></i>
                </button>
            </div>
        </div>
    </div>;
};

export { setDependencies, submitHandler, getSocietyUsers, getFlatMembers, flatModalBodyTemplate };