import BasicDetails from "./BasicDetails";
import ChangePassword from "./ChangePassword";
import SecurityQuestions from "./SecurityQuestions";

export default function UserDetails({ dependencies: [setSpinner, setAlert] }) {

    return (
        <div className="container">
            <BasicDetails dependencies={[setSpinner, setAlert]}></BasicDetails>
            <ChangePassword dependencies={[setSpinner, setAlert]}></ChangePassword>
            <SecurityQuestions dependencies={[setAlert]}></SecurityQuestions>
        </div>
    );
}
