import axios from 'axios';
import React from 'react';

let setDetails, setUsers, setModalContent, setAlert, getSocietyUsers,
    details, users, existingUsers;

const setDependencies = dependencies => {
    [setDetails, setUsers, setModalContent, setAlert, getSocietyUsers,
        details, users, existingUsers] = dependencies;
};

const submitHandler = (e, url, method) => {
    e.preventDefault();
    for (const item in details) {
        if (details[item] === "") {
            setAlert({ class: "alert-danger", message: "Please fill all the fields" });
            return;
        }
    }
    const data = { ...details };
    delete data.name;
    axios({
        url: url,
        method: method,
        data: data
    }).then(
        res => {
            setModalContent(null);
            getSocietyUsers();
            setAlert({ class: "alert-success", message: res.data.message });
        },
        err => {
            if (err.response.data.message !== undefined) {
                setAlert({ class: "alert-danger", message: err.response.data.message });
            }
            else {
                setAlert({ class: "alert-danger", message: err.response.data });
            }
        }
    );
};

const handleChange = e => {
    const { name, value, checked } = e.target;
    if (name === "email") {
        const selectedUser = users.filter(user => user.email === value.toLowerCase())[0];
        if (selectedUser) {
            setDetails({ ...details, [name]: value, name: selectedUser.name });
        }
        else {
            setDetails({ ...details, [name]: value });
        }
    }
    else if (name === "isAdmin") {
        setDetails({ ...details, [name]: checked });
    }
};

const getUsers = () => {
    axios.get("getUsers").then(
        res => {
            setUsers(res.data);
        },
        err => console.log(err)
    );
};

const societyUserModalBodyTemplate = () => {
    return (
        <div className="modal-body">
            <div className="form-outline mb-4">
                <input type="text" className="form-control form-control-lg" name="email"
                    onChange={handleChange} value={details.email} list="user-suggestions" />
                <label className="form-label floating-label">Email ID</label>
            </div>
            <datalist id="user-suggestions">
                {users.length > 0 && users.filter(user =>
                    !(existingUsers.map(exisUser => exisUser.email)).includes(user.email)).map((user, i) =>
                        <option key={i} value={user.email}>{user.name}</option>)}
            </datalist>
            <div className="form-outline mb-4">
                <input type="text" className="form-control form-control-lg" name="name"
                    value={details.name} disabled />
                <label className="form-label floating-label">Name</label>
            </div>
            <div className="form-check d-flex justify-content-start mb-4">
                <input
                    className="form-check-input"
                    name="isAdmin"
                    type="checkbox"
                    onChange={handleChange}
                    checked={details.isAdmin}
                    id="isAdmin"
                />
                <label className="form-check-label" htmlFor="isAdmin"
                    style={{ padding: '0rem 1rem' }}>Admin</label>
            </div>
        </div>
    );
};

export { setDependencies, submitHandler, getUsers, societyUserModalBodyTemplate };
