import React, { useRef, useState, useEffect } from 'react';
import { Navigate, useParams } from 'react-router-dom';
import { Card, AddCard, AddFlatModal, EditFlatModal, DeleteModal, ViewFlatModal, TransferDueAmount } from '../../components/components';
import axios from 'axios';

import './FlatList.css';

export default function FlatList({ dependencies: [setModalContent, setSpinner, setAlert] }) {
    const { societyId } = useParams();
    const [flats, setFlats] = useState([]);
    const currency = useRef("");
    const [permissionError, setPermissionError] = useState(false);
    const cardTxtClassList = "d-flex align-content-center justify-content-between w-100 py-1";

    const getSocietyFlats = () => {
        setSpinner(true);
        axios.get(`getSocietyFlats/${societyId}`).then(
            res => {
                currency.current = res.data.currency;
                setFlats(res.data.flats);
                setSpinner(false);
            },
            err => {
                if (err.response.staus === 403) {
                    setPermissionError(true);
                }
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
                setSpinner(false);
            }
        );
    };

    useEffect(() => {
        getSocietyFlats();
    }, []);

    return (
        <div>
            {(() => permissionError ? <Navigate to={"/"}></Navigate> : <>
                <div className="d-flex justify-content-center position-relative">
                    <h3 className="d-flex justify-content-center">
                        <span className='text-watermark'>Flats</span>
                    </h3>
                    <span className='badge rounded-pill bg-warning text-dark position-absolute top-0 end-0 me-2' style={{height: "fit-content"}}>Flat Count: {flats.length}</span>
                </div>
                <div className="row m-0 justify-content-center">
                    {flats.length > 0 && flats.map(flat => {
                        return (
                            <Card key={flat.id} id={flat.id}
                                header={<><div
                                    role="button"
                                    onClick={() => setModalContent(<ViewFlatModal
                                        initialDetails={{ id: flat.id, code: flat.code, area: flat.area }}
                                        setModalContent={setModalContent} />)}>
                                    {flat.code}
                                </div>
                                    <div className="btn-sm btn-secondary position-absolute top-0 end-0 me-3 mt-2">{flat.area} sqft</div>
                                </>}
                                body={flat.owner ? <>
                                    <div className={cardTxtClassList}>
                                        <span className="fw-bold">Owner:</span><span className="btn-sm btn-primary">{flat.owner}</span>
                                    </div>
                                    <div className={cardTxtClassList}>
                                        <span className="fw-bold">Due Amount:</span>
                                        <span className={`shadow btn-sm ${flat.dueAmount <= 0 ? "btn-success" : "btn-danger"}`}
                                            role="button" onClick={() => setModalContent(<TransferDueAmount
                                                senderFlat={flat}
                                                flats={flats}
                                                setModalContent={setModalContent}
                                                setAlert={setAlert}
                                                getSocietyFlats={getSocietyFlats} />)}>
                                            {currency.current} {flat.dueAmount}</span>
                                    </div>
                                </> : <div className="text-center">No members yet</div>}
                                footer={<>
                                    <i className="btn col-auto glyphicon fa fa-edit text-primary"
                                        onClick={() => setModalContent(<EditFlatModal
                                            initialDetails={{ id: flat.id, code: flat.code, area: flat.area }}
                                            societyId={societyId}
                                            setModalContent={setModalContent}
                                            setAlert={setAlert}
                                            getSocietyFlats={getSocietyFlats} />)}></i>
                                    <i className="btn col-auto glyphicon fas fa-trash-alt text-danger"
                                        onClick={() => setModalContent(<DeleteModal
                                            deleteUrl={`deleteFlat/${flat.id}`}
                                            entityName={flat.code}
                                            setModalContent={setModalContent}
                                            setAlert={setAlert}
                                            getEntities={getSocietyFlats} />)}></i>
                                </>
                                }></Card>
                        );
                    })}
                    <AddCard setModalContent={setModalContent} setAlert={setAlert}
                        modalContent={<AddFlatModal
                            societyId={societyId}
                            setModalContent={setModalContent}
                            setAlert={setAlert}
                            getSocietyFlats={getSocietyFlats} />} />
                </div>
            </>
            )()}
        </div>
    );
}