import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { UpdateIncomeModal, ViewIncomeModal } from '../../components/components';

import './Incomes.css';

export default function Incomes({ dependencies: [setModalContent, setSpinner, setAlert] }) {
  const { collectionId } = useParams();
  const [incomeData, setIncomeData] = useState({
    currency: "",
    totalExpected: "",
    totalReceived: "",
    incomes: []
  });

  const getIncomes = () => {
    setSpinner(true);
    axios.get(`getIncomes/${collectionId}`).then(
      res => {
        setIncomeData(res.data);
        setSpinner(false);
      },
      err => {
        if (err.response.data.message !== undefined) {
          setAlert({ class: "alert-danger", message: err.response.data.message });
        }
        else {
          setAlert({ class: "alert-danger", message: err.response.data });
        }
        setSpinner(false);
      }
    );
  };

  useEffect(() => {
    getIncomes();
  }, []);

  return (
    <>
      <h3 className="d-flex justify-content-center">
        <span className='text-watermark'>Incomes</span>
      </h3>
      <div className="card shadow mx-3 table-responsive">
        <table className="table table-striped mb-2">
          <thead>
            <tr>
              <th scope="col">Flat Code</th>
              <th scope="col">Owner</th>
              <th scope="col">Expected Amount</th>
              <th scope="col">Status</th>
              <th scope="col">Action</th>
            </tr>
          </thead>
          <tbody>
            {incomeData.incomes.length > 0 && incomeData.incomes.map(income => {
              return <tr key={income.id}>
                <td>
                  <div role="button" className="btn-sm shadow btn-primary" onClick={() => setModalContent(<ViewIncomeModal
                    incomeId={income.id}
                    expectedAmount={income.expectedAmount}
                    setModalContent={setModalContent}
                    setAlert={setAlert}
                    getIncomes={getIncomes} />)}>
                    {income.flatCode}
                  </div>
                </td>
                <td>{income.owner}</td>
                <td>{income.expectedAmount}</td>
                <td>
                  <div className={`btn-sm ${income.isPaid ? "btn-success" : "btn-danger"}`}>
                    {income.isPaid ? "Paid" : "Due"}
                  </div>
                </td>
                <td><i role="button" className="btn-sm fa fa-edit text-primary"
                  onClick={() => setModalContent(<UpdateIncomeModal
                    incomeId={income.id}
                    expectedAmount={income.expectedAmount}
                    setModalContent={setModalContent}
                    setAlert={setAlert}
                    getIncomes={getIncomes} />)}
                ></i></td>
              </tr>;
            })

            }
            <tr className="bg-dark">
              <td className="text-white">Total</td>
              <td></td>
              <td className="text-white">{incomeData.totalExpected}</td>
              <td></td>
              <td></td>
            </tr>
          </tbody>
        </table>
      </div>
    </>
  );
}
