import axios from 'axios';
import { getLongDate } from '../../../../others/helper';

let setModalContent, setAlert, getExpenses, setDetails, setErrors,
  setSenderAccounts, setReceiverAccounts, setPaymentMethods, details,
  errors, senderAccounts, receiverAccounts, paymentMethods;

const setDependencies = dependencies => {
  [setModalContent, setAlert, getExpenses, setDetails, setErrors,
    setSenderAccounts, setReceiverAccounts, setPaymentMethods, details,
    errors, senderAccounts, receiverAccounts, paymentMethods] = dependencies;
};

const setDetailsForExpenseTransaction = expenseId => {
  axios.get(`getDetailsForExpenseTransaction/${expenseId}`).then(
    res => {
      const defaultDetails = {};
      if (res.data.currentDetails.receiverId) {
        defaultDetails["senderEmail"] = res.data.newOptions.senderAccounts.filter(
          account => account.id === res.data.currentDetails.senderId)[0].email;
      }
      setSenderAccounts(res.data.newOptions.senderAccounts);
      setReceiverAccounts(res.data.newOptions.receiverAccounts);
      setPaymentMethods(res.data.newOptions.paymentMethods);
      setDetails({ ...details, ...res.data.currentDetails, ...defaultDetails });
    },
    err => console.log(err)
  );
};

const submitHandler = (e, url, method) => {
  e.preventDefault();
  details.senderId = senderAccounts.filter(account => account.email === details.senderEmail)[0].id;
  for (const item in details) {
    if (details[item] === "" && item !== "transactionComment") {
      setAlert({ class: "alert-danger", message: "Please fill all the fields" });
      return;
    }
  }
  for (const item in errors) {
    if (errors[item] !== "") {
      setAlert({ class: "alert-danger", message: "Please fill correct details" });
      return;
    }
  }
  const data = { ...details };
  delete data.senderEmail;
  axios({
    url: url,
    method: method,
    data: data
  }).then(
    res => {
      setModalContent(null);
      getExpenses();
      setAlert({ class: "alert-success", message: res.data.message });
    },
    err => {
      if (err.response.data.message !== undefined) {
        setAlert({ class: "alert-danger", message: err.response.data.message });
      }
      else {
        setAlert({ class: "alert-danger", message: err.response.data });
      }
    }
  );
};

const handleChange = e => {
  const { name, value } = e.target;
  setDetails({ ...details, [name]: value });
};

const validateFields = e => {
  const { name, value } = e.target;
  if (value === "") {
    setErrors({ ...errors, [name]: "Please enter a value" });
    return;
  }
  setErrors({ ...errors, [name]: "" });
};

const getExpenseModalBodyTemplate = () => {
  return <div className="modal-body">
    <div className="form-outline">
      <input type="number" className="form-control form-control-lg hidden-arrows" name="amount"
        onChange={handleChange} onBlur={validateFields}
        value={details.amount} />
      <label className="form-label floating-label">Amount</label>
    </div>
    <div className="validation-error">{errors.amount}</div>
    <div className="form-outline">
      <select className="form-select form-select-lg" name="receiverId" data-value={details.receiverId}
        onChange={handleChange} value={details.receiverId} onBlur={validateFields}>
        <option style={{ display: "none" }}></option>
        {receiverAccounts.map((item, i) => <option key={i} value={item.id}>{item.name}</option>)}
      </select>
      <label className="form-label floating-label">Expense Head</label>
    </div>
    <div className="validation-error">{errors.receiverId}</div>
    <div className="form-outline">
      <input type="text" className="form-control form-control-lg" name="senderEmail"
        onChange={handleChange} onBlur={validateFields}
        value={details.senderEmail} list="sender-account-suggestions" />
      <datalist id="sender-account-suggestions">
        {senderAccounts.length > 0 && senderAccounts.map((senderAccount, i) =>
          <option key={i} value={senderAccount.email}>{senderAccount.name}</option>)}
      </datalist>
      <label className="form-label floating-label">Sender</label>
    </div>
    <div className="validation-error">{errors.senderEmail}</div>
    <div className="form-outline">
      <input type="date" className="form-control form-control-lg datepicker" name="date"
        onChange={handleChange} value={details.date}
        onBlur={validateFields} max={(new Date()).toISOString().split("T")[0]} />
      <label className="date-label">{getLongDate(details.date)}</label>
      <label className="form-label floating-label">Payment Date</label>
    </div>
    <div className="validation-error">{errors.date}</div>
    <div className="form-outline">
      <select className="form-select form-select-lg" name="methodId" data-value={details.methodId}
        onChange={handleChange} value={details.methodId} onBlur={validateFields}>
        <option style={{ display: "none" }}></option>
        {paymentMethods.map((item, i) => <option key={i} value={item.id}>{item.method}</option>)}
      </select>
      <label className="form-label floating-label">Payment Method</label>
    </div>
    <div className="validation-error">{errors.methodId}</div>
    <div className="form-outline">
      <textarea type="text" className="form-control form-control-lg" name="transactionComment" data-value={details.transactionComment}
        onChange={handleChange} value={details.transactionComment} />
      <label className="form-label floating-label">Comment</label>
    </div>
  </div>;
};

export { setDependencies, submitHandler, setDetailsForExpenseTransaction, getExpenseModalBodyTemplate };