import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { getLongDate } from '../../../../others/helper';

import '../../../../pages/UserForms/Forms.css';

export default function UpdateIncome({ incomeId, expectedAmount, setModalContent, setAlert, getIncomes }) {
    const [details, setDetails] = useState({
        id: incomeId,
        senderId: "",
        amount: 0,
        receiverId: "",
        date: "",
        methodId: "",
        transactionComment: ""
    });
    const [errors, setErrors] = useState({});
    const [accountSuggestions, setAccountSuggestions] = useState([]);
    const [paymentMethods, setPaymentMethods] = useState([]);

    const submitHandler = e => {
        e.preventDefault();
        details.receiverId = accountSuggestions.filter(account => account.email === details.receiverEmail)[0].id;
        for (const item in details) {
            if (details[item] === "" && item !== "transactionComment") {
                setAlert({ class: "alert-danger", message: "Please fill all the fields" });
                return;
            }
        }
        for (const item in errors) {
            if (errors[item] !== "") {
                setAlert({ class: "alert-danger", message: "Please fill correct details" });
                return;
            }
        }
        const putDetails = { ...details };
        delete putDetails.receiverEmail;
        axios.put('updateIncome', putDetails).then(
            res => {
                setModalContent(null);
                getIncomes();
                setAlert({ class: "alert-success", message: res.data.message });
            },
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
            }
        );
    };

    const handleChange = e => {
        const { name, value } = e.target;
        setDetails({ ...details, [name]: value });
    };

    const validateFields = e => {
        const { name, value } = e.target;
        if (value === "") {
            setErrors({ ...errors, [name]: "Please enter a value" });
            return;
        }
        setErrors({ ...errors, [name]: "" });
    };

    useEffect(() => {
        axios.get(`getDetailsForIncomeTransaction/${incomeId}`).then(
            res => {
                const defaultDetails = {};
                if (!res.data.currentDetails.amount) {
                    defaultDetails["amount"] = expectedAmount;
                }
                if (res.data.currentDetails.receiverId) {
                    defaultDetails["receiverEmail"] = res.data.newOptions.receiverAccounts.filter(
                        account => account.id === res.data.currentDetails.receiverId)[0].email;
                }
                setDetails({ ...details, ...res.data.currentDetails, ...defaultDetails });
                setAccountSuggestions(res.data.newOptions.receiverAccounts);
                setPaymentMethods(res.data.newOptions.paymentMethods);
            },
            err => console.log(err)
        );
    }, []);
    return (
        <div className="modal-content">
            <div className="modal-header">
                <h5 className="modal-title">Update Income</h5>
                <button type="button" className="btn-close" onClick={() => setModalContent(null)}>
                </button>
            </div>
            <form onSubmit={submitHandler}>
                <div className="modal-body">
                    <div className="form-outline">
                        <input type="number" className="form-control form-control-lg hidden-arrows" name="amount"
                            onChange={handleChange} onBlur={validateFields}
                            value={details.amount} />
                        <label className="form-label floating-label">Amount</label>
                    </div>
                    <div className="validation-error">{errors.amount}</div>
                    <div className="form-outline">
                        <input type="text" className="form-control form-control-lg" name="receiverEmail"
                            onChange={handleChange} onBlur={validateFields}
                            value={details.receiverEmail} list="account-suggestions" />
                        <datalist id="account-suggestions">
                            {accountSuggestions.length > 0 && accountSuggestions.map((suggestion, i) => {
                                return <option key={i} value={suggestion.email}>{suggestion.name}</option>;
                            })}
                        </datalist>
                        <label className="form-label floating-label">Receiver</label>
                    </div>
                    <div className="validation-error">{errors.receiverEmail}</div>
                    <div className="form-outline">
                        <input type="date" className="form-control form-control-lg datepicker" name="date"
                            onChange={handleChange} value={details.date}
                            onBlur={validateFields} max={(new Date()).toISOString().split("T")[0]} />
                        <label className="date-label">{getLongDate(details.date)}</label>
                        <label className="form-label floating-label">Payment Date</label>
                    </div>
                    <div className="validation-error">{errors.date}</div>
                    <div className="form-outline">
                        <select className="form-select form-select-lg" name="methodId" data-value={details.methodId}
                            onChange={handleChange} value={details.methodId} onBlur={validateFields}>
                            <option style={{ display: "none" }}></option>
                            {paymentMethods.map((item, i) => <option key={i} value={item.id}>{item.method}</option>)}
                        </select>
                        <label className="form-label floating-label">Payment Method</label>
                    </div>
                    <div className="validation-error">{errors.methodId}</div>
                    <div className="form-outline">
                        <textarea type="text" className="form-control form-control-lg" name="transactionComment" data-value={details.transactionComment}
                            onChange={handleChange} value={details.transactionComment}
                            onBlur={validateFields} />
                        <label className="form-label floating-label">Comment</label>
                    </div>
                </div>
                <div className="form-outline modal-footer d-flex justify-content-between">
                    <button type="submit" className="btn btn-primary">Update</button>
                    <button type="button" className="btn btn-primary" onClick={() => setModalContent(null)}>Cancel</button>
                </div>
            </form>
        </div>
    );
}
