import React, {useState, useEffect} from 'react';
import { setDependencies, submitHandler, getUsers, societyUserModalBodyTemplate } from './SocietyUserModal';

export default function EditSocietyUserModal({initialDetails, existingUsers, setModalContent, setAlert, getSocietyUsers}) {
    const [details, setDetails] = useState(initialDetails);
    const [users, setUsers] = useState([]);

    setDependencies([setDetails, setUsers, setModalContent, setAlert, getSocietyUsers,
        details, users, existingUsers]);
    
    useEffect(()=>{
        getUsers();
    }, []);
    
    return (
        <div className="modal-content">
            <div className="modal-header">
                <h5 className="modal-title">Change User</h5>
                <button type="button" className="btn-close" onClick={() => setModalContent(null)}>
                </button>
            </div>
            <form onSubmit={e => submitHandler(e, 'updateSocietyUser', 'PUT')}>
                {societyUserModalBodyTemplate()}
                <div className="form-outline modal-footer d-flex justify-content-between">
                    <button type="submit" className="btn btn-primary">Update</button>
                    <button type="button" className="btn btn-primary" onClick={() => setModalContent(null)}>Cancel</button>
                </div>
            </form>
        </div>
    );
}
