import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { setDependencies, submitHandler, getExpenseModalBodyTemplate } from './ExpenseModal';

export default function AddExpenseModal({ collectionId, setModalContent, setAlert, getExpenses }) {
  const [details, setDetails] = useState({
    collectionId: collectionId,
    amount: "",
    senderId: "",
    receiverId: "",
    date: "",
    methodId: "",
    transactionComment: ""
  });
  const [errors, setErrors] = useState({});
  const [senderAccounts, setSenderAccounts] = useState([]);
  const [receiverAccounts, setReceiverAccounts] = useState([]);
  const [paymentMethods, setPaymentMethods] = useState([]);

  setDependencies([setModalContent, setAlert, getExpenses, setDetails, setErrors,
    setSenderAccounts, setReceiverAccounts, setPaymentMethods, details,
    errors, senderAccounts, receiverAccounts, paymentMethods]);
  
  useEffect(() => {
    axios.get(`getDetailsForAddExpenseTransaction/${collectionId}`).then(
      res => {
        setSenderAccounts(res.data.newOptions.senderAccounts);
        setReceiverAccounts(res.data.newOptions.receiverAccounts);
        setPaymentMethods(res.data.newOptions.paymentMethods);
      },
      err => console.log(err)
    );
  }, []);

  return (
    <div className="modal-content">
      <div className="modal-header">
        <h5 className="modal-title">Add Expense</h5>
        <button type="button" className="btn-close" onClick={() => setModalContent(null)}>
        </button>
      </div>
      <form onSubmit={e=> submitHandler(e, "addExpense", "POST")}>
        {getExpenseModalBodyTemplate()}
        <div className="form-outline modal-footer d-flex justify-content-between">
          <button type="submit" className="btn btn-primary">Add</button>
          <button type="button" className="btn btn-primary" onClick={() => setModalContent(null)}>Cancel</button>
        </div>
      </form>
    </div>
  );
}
