import React from 'react';
import { Navigate } from 'react-router-dom';

import SocietyList from './SocietyList';

export default React.memo(function Home({ dependencies: [user, setModalContent, setSpinner, setAlert] }) {

    return (
        <>{
            (() => {
                if (user.user_full_name !== "") {
                    return (
                        <div>
                            <SocietyList dependencies={[setModalContent, setSpinner, setAlert]}/>
                        </div>
                    );
                }
                else {
                    return <Navigate to={'/login'}></Navigate>;
                }
            })()
        }
        </>
    );
});