import React, { useState, useRef, useEffect } from 'react';
import { Navigate } from 'react-router-dom';

import { setDependencies, submitHandler, getSocietyUsers, getFlatMembers, flatModalBodyTemplate } from './FlatModals';

import '../../../../pages/UserForms/Forms.css';
import './FlatModals.css';

export default function EditFlatModal({ initialDetails, societyId, setModalContent, setAlert, getSocietyFlats }) {
    const [details, setDetails] = useState(initialDetails);
    const users = useRef([]);
    const [errors, setErrors] = useState({});
    const [selectedFlatMembers, setSelectedFlatMembers] = useState([]);
    const [userSuggestions, setUserSuggestions] = useState([]);

    setDependencies([setDetails, setErrors, setUserSuggestions, setSelectedFlatMembers,
        setModalContent, setAlert, getSocietyFlats, userSuggestions, selectedFlatMembers, details, users, errors]);

    useEffect(() => {
        getSocietyUsers(societyId);
        getFlatMembers(initialDetails.id);
    }, []);

    return (alert && alert.class === "alert-success") ? (<Navigate to={'/'} />) : (<div className="modal-content">
        <div className="modal-header">
            <h5 className="modal-title">Edit Flat</h5>
            <button type="button" className="btn-close" onClick={() => setModalContent(null)}>
            </button>
        </div>
        <form onSubmit={e => submitHandler(e, 'updateFlat', 'PUT')}>
            {flatModalBodyTemplate()}
            <div className="form-outline modal-footer d-flex justify-content-between">
                <button type="submit" className="btn btn-primary">Save</button>
                <button type="button" className="btn btn-primary" onClick={() => setModalContent(null)}>Cancel</button>
            </div>
        </form>
    </div>);
}