import axios from 'axios';
import React, { useState } from 'react';

export default function TransferDueAmount({ senderFlat, flats, setModalContent, setAlert, getSocietyFlats }) {
    const [details, setDetails] = useState({
        senderFlatId: senderFlat.id,
        receiverFlatCode: "",
        amount: senderFlat.dueAmount,
        comment: ""
    });
    const [errors, setErrors] = useState({});

    const submitHandler = e => {
        e.preventDefault();
        for (const item in details) {
            if (details[item] === "" && item!=="comment") {
                console.log(details);
                setAlert({ class: "alert-danger", message: "Please fill all the fields" });
                return;
            }
        }
        for (const item in errors) {
            if (errors[item] !== "") {
                setAlert({ class: "alert-danger", message: "Please fill correct details" });
                return;
            }
        }
        const data = { ...details };
        data['receiverFlatId'] = flats.filter(flat => flat.code === details.receiverFlatCode)[0].id;
        delete data.receiverFlatCode;
        axios.post("transferDueAmount", data).then(
            res => {
                setModalContent(null);
                getSocietyFlats();
                setAlert({ class: "alert-success", message: res.data.message });
            },
            err => {
                if (err.response.data.message !== undefined) {
                    setAlert({ class: "alert-danger", message: err.response.data.message });
                }
                else {
                    setAlert({ class: "alert-danger", message: err.response.data });
                }
            }
        );
    };

    const handleChange = e => {
        const { name, value } = e.target;
        setDetails({ ...details, [name]: value });
    };

    const validateFields = e => {
        const { name, value } = e.target;
        if (name !== "comment" && value === "") {
            setErrors({ ...errors, [name]: "Please enter a value" });
            return;
        }
        setErrors({ ...errors, [name]: "" });
    };

    return (
        <div className="modal-content">
            <div className="modal-header">
                <h5 className="modal-title">Transfer due amount</h5>
                <button type="button" className="btn-close" onClick={() => setModalContent(null)}>
                </button>
            </div>
            <form onSubmit={submitHandler}>
                <div className="modal-body">
                    <div className="form-outline">
                        <input type="number" className="form-control form-control-lg hidden-arrows" name="amount"
                            onChange={handleChange} value={details.amount} onBlur={validateFields} />
                        <label className="form-label floating-label">Amount</label>
                    </div>
                    <div className="validation-error">{errors.amount}</div>
                    <div className="form-outline">
                        <input type="text" className="form-control form-control-lg" list="flat-suggestions" name="receiverFlatCode"
                            onChange={handleChange} value={details.receiverFlatCode} onBlur={validateFields}/>
                        <label className="form-label floating-label">Flat Code</label>
                        <datalist id="flat-suggestions">
                            {flats.length > 0 && flats.map((flat, i) =>
                                flat.owner && flat.id!==senderFlat.id && 
                                <option key={flat.id} value={flat.code}>{flat.owner}</option>)}
                        </datalist>
                    </div>
                    <div className="validation-error">{errors.recieverFlatCode}</div>
                    <div className="form-outline">
                        <input type="text" className="form-control form-control-lg" name="comment"
                            onChange={handleChange} value={details.comment} onBlur={validateFields} />
                        <label className="form-label floating-label">Comment</label>
                    </div>
                </div>
                <div className="form-outline modal-footer d-flex justify-content-between">
                    <button type="submit" className="btn btn-primary">Send</button>
                    <button type="button" className="btn btn-primary" onClick={() => setModalContent(null)}>Cancel</button>
                </div>
            </form>
        </div>

    );
}
